import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Heart, Package, Briefcase, Eye, MousePointer, Loader, Trash2 } from 'lucide-react';
import { wishlistAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import Header from '../components/Header';

const Wishlist = () => {
  const navigate = useNavigate();
  const [wishlist, setWishlist] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadWishlist();
  }, []);

  const loadWishlist = async () => {
    try {
      setLoading(true);
      const data = await wishlistAPI.getAll();
      setWishlist(data.wishlist || []);
    } catch (error) {
      console.error('Error loading wishlist:', error);
      toast.error('Failed to load wishlist');
    } finally {
      setLoading(false);
    }
  };

  const handleRemove = async (listingId, e) => {
    e.stopPropagation();
    try {
      await wishlistAPI.remove(listingId);
      setWishlist(wishlist.filter(item => item.listing_id !== listingId));
      toast.success('Removed from wishlist');
    } catch (error) {
      console.error('Error removing from wishlist:', error);
      toast.error('Failed to remove');
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black star-particles">
        <Loader className="w-8 h-8 animate-spin text-purple-500" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      
      <div className="pt-16 pb-24 px-4 relative z-10">
        <div className="max-w-7xl mx-auto">
          <div className="mb-6">
            <h1 className="text-3xl font-bold text-white font-inter">My Wishlist</h1>
            <p className="text-gray-300 text-sm mt-1 font-inter">
              {wishlist.length} saved item{wishlist.length !== 1 ? 's' : ''}
            </p>
          </div>

          {wishlist.length === 0 ? (
            <div className="bg-gray-800 border border-gray-700 rounded-2xl shadow-sm p-12 text-center">
              <div className="w-20 h-20 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-4">
                <Heart className="w-10 h-10 text-white" />
              </div>
              <h3 className="text-xl font-semibold text-white mb-2 font-inter">Your wishlist is empty</h3>
              <p className="text-gray-400 mb-6 font-inter">Start saving products and services you love</p>
              <button
                onClick={() => navigate('/')}
                className="bg-purple-600 hover:bg-purple-700 text-white px-8 py-3 rounded-xl font-semibold transition-all inline-flex items-center gap-2 font-inter"
              >
                Browse Listings
              </button>
            </div>
          ) : (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
              {wishlist.map(item => (
                <div
                  key={item.listing_id}
                  className="bg-gray-800 border border-gray-700 rounded-2xl shadow-sm hover:shadow-lg transition-all overflow-hidden group cursor-pointer relative"
                  onClick={() => navigate(`/listing/${item.listing_id}`)}
                >
                  {/* Remove Button */}
                  <button
                    onClick={(e) => handleRemove(item.listing_id, e)}
                    className="absolute top-3 right-3 z-10 bg-red-500 hover:bg-red-600 text-white p-2 rounded-full shadow-lg transition-all"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>

                  {/* Thumbnail */}
                  <div className="relative h-48 bg-gray-700">
                    {item.thumbnail_url ? (
                      <img
                        src={item.thumbnail_url}
                        alt={item.product_name}
                        className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-purple-600 to-purple-800">
                        {item.type === 'PRODUCT' ? (
                          <Package className="w-16 h-16 text-white" />
                        ) : (
                          <Briefcase className="w-16 h-16 text-white" />
                        )}
                      </div>
                    )}
                    <div className="absolute top-3 left-3 bg-black/90 backdrop-blur-sm px-3 py-1 rounded-full text-sm font-medium font-inter text-white">
                      {item.type === 'PRODUCT' ? ' Product' : ' Service'}
                    </div>
                  </div>

                  {/* Content */}
                  <div className="p-4">
                    <h3 className="font-bold text-lg text-white mb-1 line-clamp-1 font-inter">
                      {item.product_name}
                    </h3>
                    <p className="text-purple-400 font-bold text-xl mb-2 font-inter">
                      {Number(item.price).toLocaleString()}
                    </p>
                    <p className="text-gray-400 text-sm line-clamp-2 mb-3 font-inter">
                      {item.description}
                    </p>

                    {/* Provider */}
                    <p className="text-sm text-gray-500 mb-3 font-inter">
                      by {item.provider_name || item.business_name}
                    </p>

                    {/* Stats */}
                    <div className="flex items-center gap-4 text-sm text-gray-500">
                      <div className="flex items-center gap-1">
                        <Eye className="w-4 h-4" />
                        <span className="font-inter">{item.view_count || 0}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <Heart className="w-4 h-4" />
                        <span className="font-inter">{item.love_count || 0}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <MousePointer className="w-4 h-4" />
                        <span className="font-inter">{item.click_count || 0}</span>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

    </div>
  );
};

export default Wishlist;

