import React, { useState, useEffect } from 'react';
import { Edit3, User, Building2, CreditCard, Settings, Phone, MapPin, Globe, Calendar, Shield } from 'lucide-react';
import { userAPI, providerAPI, subscriptionAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import EditProfileModal from '../components/EditProfileModal';
import { useNavigate } from 'react-router-dom';

const Profile = () => {
  const [user, setUser] = useState(null);
  const [provider, setProvider] = useState(null);
  const [subscription, setSubscription] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showEditModal, setShowEditModal] = useState(false);
  const [editType, setEditType] = useState('user'); // 'user' or 'provider'
  const navigate = useNavigate();

  useEffect(() => {
    loadProfileData();
  }, []);

  const loadProfileData = async () => {
    try {
      setLoading(true);
      const [userData, providerData, subscriptionData] = await Promise.all([
        userAPI.getProfile(),
        providerAPI.getMe().catch(() => null), // Provider might not exist
        subscriptionAPI.getStatus().catch(() => null) // Subscription might not exist
      ]);

      setUser(userData);
      setProvider(providerData);
      setSubscription(subscriptionData);
    } catch (error) {
      console.error('Error loading profile:', error);
      toast.error('Failed to load profile data');
    } finally {
      setLoading(false);
    }
  };

  const handleEditSuccess = () => {
    loadProfileData();
    setShowEditModal(false);
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    return new Date(dateString).toLocaleDateString('en-IN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const getSubscriptionStatus = () => {
    if (!subscription) return { status: 'Free', color: 'text-gray-400' };
    
    const now = new Date();
    const expiresAt = new Date(subscription.expires_at);
    
    if (expiresAt > now) {
      return { status: 'Active', color: 'text-green-400' };
    } else {
      return { status: 'Expired', color: 'text-red-400' };
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center star-particles">
        <div className="text-white text-lg">Loading profile...</div>
      </div>
    );
  }

  const subscriptionStatus = getSubscriptionStatus();

  return (
    <div className="min-h-screen bg-black text-white star-particles">
      {/* Header */}
      <div className="bg-black border-b border-gray-700 px-6 py-4 shadow-sm relative z-10">
        <div className="flex items-center justify-between">
          <h1 className="text-xl font-bold text-white">Profile</h1>
          <button
            onClick={() => {
              setEditType('user');
              setShowEditModal(true);
            }}
            className="flex items-center gap-2 bg-purple-600 hover:bg-purple-700 text-white px-4 py-2 rounded-lg transition-colors"
          >
            <Edit3 className="w-4 h-4" />
            Edit
          </button>
        </div>
      </div>

      <div className="px-6 py-6 pb-20 space-y-6 relative z-10">
        {/* User Information */}
        <div className="bg-gray-800 rounded-xl p-6 border border-gray-700 shadow-sm">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-12 h-12 bg-purple-600 rounded-full flex items-center justify-center">
              <User className="w-6 h-6 text-white" />
            </div>
            <div>
              <h2 className="text-lg font-semibold text-white">Personal Information</h2>
              <p className="text-gray-400 text-sm">Your account details</p>
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-gray-400 text-sm">Name</label>
              <p className="text-white font-medium">{user?.name || 'Not provided'}</p>
            </div>
            <div>
              <label className="text-gray-400 text-sm">Phone</label>
              <p className="text-white font-medium">{user?.phone || 'Not provided'}</p>
            </div>
            <div>
              <label className="text-gray-400 text-sm">Email</label>
              <p className="text-white font-medium">{user?.email || 'Not provided'}</p>
            </div>
            <div>
              <label className="text-gray-400 text-sm">Joined</label>
              <p className="text-white font-medium">{formatDate(user?.created_at)}</p>
            </div>
          </div>
        </div>

        {/* Provider Information */}
        {provider ? (
          <div className="bg-gray-800 rounded-xl p-6 border border-gray-700 shadow-sm">
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-3">
                <div className="w-12 h-12 bg-purple-500 rounded-full flex items-center justify-center">
                  <Building2 className="w-6 h-6 text-white" />
                </div>
                <div>
                  <h2 className="text-lg font-semibold text-white">Provider Profile</h2>
                  <p className="text-gray-400 text-sm">Your business information</p>
                </div>
              </div>
              <button
                onClick={() => {
                  setEditType('provider');
                  setShowEditModal(true);
                }}
                className="flex items-center gap-2 bg-purple-500 hover:bg-purple-600 text-white px-3 py-2 rounded-lg transition-colors text-sm"
              >
                <Edit3 className="w-4 h-4" />
                Edit
              </button>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="text-gray-400 text-sm">Business Name</label>
                <p className="text-white font-medium">{provider.business_name || 'Not provided'}</p>
              </div>
              <div>
                <label className="text-gray-400 text-sm">Provider Type</label>
                <p className="text-white font-medium capitalize">{provider.type || 'Not specified'}</p>
              </div>
              <div>
                <label className="text-gray-400 text-sm">Location</label>
                <p className="text-white font-medium">{provider.location || 'Not provided'}</p>
              </div>
              <div>
                <label className="text-gray-400 text-sm">GST Number</label>
                <p className="text-white font-medium">{provider.gst_number || 'Not provided'}</p>
              </div>
              <div className="md:col-span-2">
                <label className="text-gray-400 text-sm">Business Address</label>
                <p className="text-white font-medium">{provider.business_address || 'Not provided'}</p>
              </div>
              <div className="md:col-span-2">
                <label className="text-gray-400 text-sm">Handle/Website</label>
                <p className="text-white font-medium">{provider.handle_address || 'Not provided'}</p>
              </div>
            </div>
          </div>
        ) : (
          <div className="bg-gray-800 rounded-xl p-6 border border-gray-700 shadow-sm">
            <div className="flex items-center gap-3 mb-4">
              <div className="w-12 h-12 bg-gray-600 rounded-full flex items-center justify-center">
                <Building2 className="w-6 h-6 text-white" />
              </div>
              <div>
                <h2 className="text-lg font-semibold text-white">Provider Profile</h2>
                <p className="text-gray-400 text-sm">Register as a brand or influencer</p>
              </div>
            </div>
            <p className="text-gray-400 mb-4">
              You haven't registered as a provider yet. Register to start listing your products and services.
            </p>
            <button
              onClick={() => window.location.href = '/provider/register'}
              className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-3 rounded-lg transition-colors font-medium"
            >
              Register as Provider
            </button>
          </div>
        )}

        {/* Subscription Information */}
        <div className="bg-gray-800 rounded-xl p-6 border border-gray-700 shadow-sm">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-12 h-12 bg-purple-500 rounded-full flex items-center justify-center">
              <CreditCard className="w-6 h-6 text-white" />
            </div>
            <div>
              <h2 className="text-lg font-semibold text-white">Subscription</h2>
              <p className="text-gray-400 text-sm">Your plan and usage</p>
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-gray-400 text-sm">Status</label>
              <p className={`font-medium ${subscriptionStatus.status === 'Active' ? 'text-green-400' : subscriptionStatus.status === 'Expired' ? 'text-red-400' : 'text-gray-400'}`}>
                {subscriptionStatus.status}
              </p>
            </div>
            <div>
              <label className="text-gray-400 text-sm">Uploads Remaining</label>
              <p className="text-white font-medium">
                {user?.uploads_remaining || 0} / {user?.subscription_status === 'premium' ? 'Unlimited' : '5'}
              </p>
            </div>
            <div>
              <label className="text-gray-400 text-sm">Total Uploads</label>
              <p className="text-white font-medium">{user?.total_uploads || 0}</p>
            </div>
            <div>
              <label className="text-gray-400 text-sm">Expires</label>
              <p className="text-white font-medium">
                {subscription ? formatDate(subscription.expires_at) : 'N/A'}
              </p>
            </div>
          </div>

          {subscriptionStatus.status === 'Free' && (
            <div className="mt-4 p-4 bg-purple-900/50 rounded-lg border border-purple-700">
              <h3 className="font-semibold text-purple-400 mb-2">Upgrade to Premium</h3>
              <p className="text-gray-300 text-sm mb-3">
                Get unlimited uploads and premium features to grow your business.
              </p>
              <button
                onClick={() => window.location.href = '/listing'}
                className="bg-purple-600 hover:bg-purple-700 text-white px-4 py-2 rounded-lg transition-colors text-sm"
              >
                View Plans
              </button>
            </div>
          )}
        </div>

        {/* Account Settings */}
        <div className="bg-gray-800 rounded-xl p-6 border border-gray-700 shadow-sm">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-12 h-12 bg-gray-600 rounded-full flex items-center justify-center">
              <Settings className="w-6 h-6 text-white" />
            </div>
            <div>
              <h2 className="text-lg font-semibold text-white">Account Settings</h2>
              <p className="text-gray-400 text-sm">Manage your account</p>
            </div>
          </div>

          <div className="space-y-3">
            <button className="w-full text-left p-3 bg-gray-700 hover:bg-gray-600 rounded-lg transition-colors">
              <div className="flex items-center gap-3">
                <Phone className="w-5 h-5 text-purple-500" />
                <div>
                  <p className="font-medium text-white">Change Phone Number</p>
                  <p className="text-gray-400 text-sm">Update your contact information</p>
                </div>
              </div>
            </button>

            <button className="w-full text-left p-3 bg-gray-700 hover:bg-gray-600 rounded-lg transition-colors">
              <div className="flex items-center gap-3">
                <Globe className="w-5 h-5 text-purple-500" />
                <div>
                  <p className="font-medium text-white">Privacy Settings</p>
                  <p className="text-gray-400 text-sm">Manage your privacy preferences</p>
                </div>
              </div>
            </button>

            <button className="w-full text-left p-3 bg-gray-700 hover:bg-gray-600 rounded-lg transition-colors">
              <div className="flex items-center gap-3">
                <Calendar className="w-5 h-5 text-purple-500" />
                <div>
                  <p className="font-medium text-white">Download Data</p>
                  <p className="text-gray-400 text-sm">Export your account data</p>
                </div>
              </div>
            </button>
          </div>
        </div>

        {/* Admin Panel Access */}
        {user?.is_admin && (
          <div className="bg-gradient-to-r from-blue-900/50 to-purple-900/50 rounded-xl p-6 border border-blue-700/50 shadow-sm">
            <div className="flex items-center gap-3 mb-4">
              <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                <Shield className="w-6 h-6 text-white" />
              </div>
              <div>
                <h2 className="text-lg font-semibold text-white">Admin Panel</h2>
                <p className="text-blue-300 text-sm">Platform administration and management</p>
              </div>
            </div>

            <div className="bg-black/20 rounded-lg p-4 mb-4">
              <p className="text-blue-200 text-sm">
                 You have admin privileges! Access the admin panel to manage users, content, subscriptions, and platform analytics.
              </p>
            </div>

            <button
              onClick={() => navigate('/admin/dashboard')}
              className="w-full bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white px-6 py-3 rounded-lg transition-all duration-200 transform hover:scale-105 font-medium flex items-center justify-center gap-2"
            >
              <Shield className="w-5 h-5" />
              Access Admin Dashboard
            </button>
          </div>
        )}
      </div>

      {/* Edit Modal */}
      {showEditModal && (
        <EditProfileModal
          type={editType}
          user={user}
          provider={provider}
          onClose={() => setShowEditModal(false)}
          onSuccess={handleEditSuccess}
        />
      )}

    </div>
  );
};

export default Profile;
