import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Package, Briefcase, Loader, ArrowLeft, List } from 'lucide-react';
import { providerAPI } from '../utils/api';
import Header from '../components/Header';

const Listing = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [hasProvider, setHasProvider] = useState(false);

  useEffect(() => {
    checkProviderProfile();
  }, []);

  const checkProviderProfile = async () => {
    try {
      setLoading(true);
      const response = await providerAPI.getMe();
      if (response.provider) {
        setHasProvider(true);
      }
    } catch (error) {
      if (error.response?.status === 404) {
        navigate('/provider/register');
      } else {
        console.error('Error checking provider:', error);
      }
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black star-particles">
        <Loader className="w-8 h-8 animate-spin text-purple-500" />
      </div>
    );
  }

  if (!hasProvider) {
    return null;
  }

  const listingOptions = [
    {
      id: 'product',
      title: 'List a Product',
      description: 'Showcase your physical products to thousands of users',
      icon: Package,
      color: 'from-purple-600 to-purple-700',
      route: '/listing/upload?type=PRODUCT'
    },
    {
      id: 'service',
      title: 'List a Service',
      description: 'Offer your services and expertise to the community',
      icon: Briefcase,
      color: 'from-gray-700 to-gray-800',
      route: '/listing/upload?type=SERVICE'
    }
  ];

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      
      <div className="pt-16 pb-24 px-4 relative z-10">
        {/* Navigation Buttons */}
        <div className="max-w-7xl mx-auto mb-6 flex gap-3">
          <button
            onClick={() => navigate('/')}
            className="flex items-center gap-2 px-4 py-2 text-gray-400 hover:text-white border border-gray-600 rounded-lg hover:bg-gray-800 transition-colors font-inter"
          >
            <ArrowLeft className="w-4 h-4" />
            Home
          </button>
          <button
            onClick={() => navigate('/listing/my')}
            className="flex items-center gap-2 px-4 py-2 text-gray-400 hover:text-white border border-gray-600 rounded-lg hover:bg-gray-800 transition-colors font-inter"
          >
            <List className="w-4 h-4" />
            My Listings
          </button>
        </div>

        {/* Header Section */}
        <div className="max-w-7xl mx-auto mb-8">
          <h1 className="text-3xl font-bold text-white mb-2 font-inter">Create a Listing</h1>
          <p className="text-gray-400 font-inter">Choose what you'd like to list</p>
        </div>

        {/* Content */}
        <div className="max-w-7xl mx-auto">
          <div className="grid md:grid-cols-2 gap-6 max-w-4xl mx-auto">
            {listingOptions.map((option) => {
              const Icon = option.icon;
              return (
                <div
                  key={option.id}
                  onClick={() => navigate(option.route)}
                  className="bg-gray-800 border border-gray-700 rounded-2xl shadow-sm hover:shadow-lg transition-all duration-300 cursor-pointer overflow-hidden group"
                >
                  <div className={`h-32 bg-gradient-to-r ${option.color} flex items-center justify-center group-hover:scale-105 transition-transform duration-300`}>
                    <Icon className="w-16 h-16 text-white" strokeWidth={1.5} />
                  </div>
                  <div className="p-6">
                    <h3 className="text-xl font-bold text-white mb-2 font-inter">{option.title}</h3>
                    <p className="text-gray-400 font-inter mb-4">{option.description}</p>
                    <button className="w-full bg-purple-600 text-white py-3 rounded-xl font-medium hover:bg-purple-700 transition-colors font-inter">
                      Get Started 
                    </button>
                  </div>
                </div>
              );
            })}
          </div>

          {/* Info Section */}
          <div className="mt-12 max-w-4xl mx-auto bg-gray-800 border border-gray-700 rounded-2xl shadow-sm p-8">
            <h2 className="text-xl font-bold text-white mb-4 font-inter">How it works</h2>
            <div className="grid md:grid-cols-3 gap-6">
              <div className="text-center">
                <div className="w-12 h-12 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-3">
                  <span className="text-white font-bold text-lg">1</span>
                </div>
                <h3 className="font-semibold text-white mb-1 font-inter">Choose Type</h3>
                <p className="text-sm text-gray-400 font-inter">Select whether you're listing a product or service</p>
              </div>
              <div className="text-center">
                <div className="w-12 h-12 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-3">
                  <span className="text-white font-bold text-lg">2</span>
                </div>
                <h3 className="font-semibold text-white mb-1 font-inter">Upload Details</h3>
                <p className="text-sm text-gray-400 font-inter">Add video, images, and information about your listing</p>
              </div>
              <div className="text-center">
                <div className="w-12 h-12 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-3">
                  <span className="text-white font-bold text-lg">3</span>
                </div>
                <h3 className="font-semibold text-white mb-1 font-inter">Go Live</h3>
                <p className="text-sm text-gray-400 font-inter">Your listing will be visible to thousands of users</p>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  );
};

export default Listing;
