// SectionCarousel.jsx - Horizontal scrollable section with reels
import React from 'react';
import ReelCard from './ReelCard';

const SectionCarousel = ({ section, onShowMore, onCardClick, wishlistIds, onToggleWishlist }) => {
  return (
    <div className="py-4 bg-black star-particles">
      <div className="px-4 mb-3 relative z-10">
        <h2 className="text-xl font-gilroy font-bold text-white">{section.title}</h2>
      </div>
      
      <div className="px-4 relative z-10">
        <div className="grid grid-cols-3 gap-3">
          {section.items.map((reel) => (
            <ReelCard 
              key={reel.id} 
              reel={reel} 
              onShowMore={onShowMore}
              onCardClick={onCardClick}
              isWishlisted={wishlistIds.includes(reel.id)}
              onToggleWishlist={onToggleWishlist}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default SectionCarousel;

