import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useSearchParams } from 'react-router-dom';
import { Eye, Heart, Share2, ShoppingCart, Play, Volume2, VolumeX, ChevronLeft, ChevronRight, Sparkles } from 'lucide-react';
import { watchAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const Watch = () => {
  const [searchParams] = useSearchParams();
  const { user, isAuthenticated } = useAuth();
  const [videos, setVideos] = useState([]);
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [videoRefs, setVideoRefs] = useState({});
  const [isPlaying, setIsPlaying] = useState(false);
  const [isMuted, setIsMuted] = useState(true);
  const [engagementCounts, setEngagementCounts] = useState({});
  const [likedItems, setLikedItems] = useState({});
  const [relatedProducts, setRelatedProducts] = useState([]);
  const [showProductModal, setShowProductModal] = useState(false);
  const [selectedProductData, setSelectedProductData] = useState(null);
  const [showShareModal, setShowShareModal] = useState(false);
  const [showStoreModal, setShowStoreModal] = useState(false);
  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);
  const [isTransitioning, setIsTransitioning] = useState(false);

  const videoContainerRef = useRef(null);

  // Create a stable ref callback to prevent infinite loops
  const setVideoRef = useCallback((index) => {
    return (el) => {
      if (el) {
        setVideoRefs(prev => {
          // Only update if the ref is different
          if (prev[index] !== el) {
            return { ...prev, [index]: el };
          }
          return prev;
        });
      }
    };
  }, []);

  useEffect(() => {
    const videoId = searchParams.get('video');
    console.log('Watch component mounted with video ID:', videoId);
    loadVideos(0, 5, true, videoId);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchParams]);

  useEffect(() => {
    if (videos.length > 0) {
      // Track view when video becomes active
      trackEngagement(videos[currentVideoIndex]?.listing_id, 'view');
      // Load related products for current video
      loadRelatedProducts(videos[currentVideoIndex]?.provider_id);
      
      // Pause all videos except current one
      Object.keys(videoRefs).forEach((key) => {
        const video = videoRefs[key];
        if (video && parseInt(key) !== currentVideoIndex) {
          video.pause();
        }
      });
      
      // Play current video
      const currentVideo = videoRefs[currentVideoIndex];
      if (currentVideo) {
        currentVideo.play().catch(console.error);
        setIsPlaying(true);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentVideoIndex, videos]);

  const loadVideos = async (offset = 0, limit = 5, isInitialLoad = true, specificVideoId = null) => {
    try {
      console.log('Loading videos...');
      if (isInitialLoad) {
        setLoading(true);
      }
      
      let response;
      
      // If we have a specific video ID, try to get that video first
      if (specificVideoId && isInitialLoad) {
        try {
          const specificVideoResponse = await watchAPI.getVideoDetails(specificVideoId);
          if (specificVideoResponse.video) {
            // Get the specific video and then load the feed with admin priority
            const feedResponse = await watchAPI.getFeedWithAdminPriority(limit, offset);
            const allVideos = [specificVideoResponse.video, ...(feedResponse.videos || [])];
            
            // Remove duplicates based on listing_id
            const uniqueVideos = allVideos.filter((video, index, self) => 
              index === self.findIndex(v => v.listing_id === video.listing_id)
            );
            
            response = { videos: uniqueVideos };
            console.log(`Loading specific video ${specificVideoId} with admin priority feed`, response.videos);
          } else {
            response = await watchAPI.getFeedWithAdminPriority(limit, offset);
          }
        } catch (error) {
          console.log('Could not fetch specific video, falling back to admin priority feed:', error);
          response = await watchAPI.getFeedWithAdminPriority(limit, offset);
        }
      } else {
        response = await watchAPI.getFeedWithAdminPriority(limit, offset);
      }
      
      console.log(`Loading videos batch: offset=${offset}, limit=${limit}`, response.videos);
      
      // Filter videos that have valid video URLs
      let validVideos = (response.videos || []).filter(video => 
        video.video_url && 
        video.video_url.trim() !== '' && 
        (video.video_url.startsWith('http') || video.video_url.startsWith('https'))
      );
      
      // If no videos with valid URLs, create sample data for testing
      if (validVideos.length === 0 && isInitialLoad) {
        console.log('No valid videos found, creating sample data...');
        validVideos = [
          {
            listing_id: 1,
            product_name: "Sample Product 1",
            video_url: "https://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4",
            thumbnail_url: "https://picsum.photos/400/600?random=1",
            provider_name: "Test Influencer",
            handle_address: "testinfluencer",
            provider_type: "INFLUENCER",
            view_count: 3500,
            like_count: 1100,
            love_count: 400,
            click_count: 456
          },
          {
            listing_id: 2,
            product_name: "Sample Product 2",
            video_url: "https://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ElephantsDream.mp4",
            thumbnail_url: "https://picsum.photos/400/600?random=2",
            provider_name: "Another Creator",
            handle_address: "anothercreator",
            provider_type: "INFLUENCER",
            view_count: 2800,
            like_count: 850,
            love_count: 222,
            click_count: 37
          }
        ];
      }
      
      if (isInitialLoad) {
        console.log('Setting videos:', validVideos);
        setVideos(validVideos);
        
        // Load like statuses for all videos
        loadLikeStatuses(validVideos);
        
        // Handle specific video ID from URL parameter
        if (specificVideoId) {
          const videoIndex = validVideos.findIndex(video => video.listing_id === parseInt(specificVideoId));
          console.log(`Looking for video ID ${specificVideoId} in videos:`, validVideos.map(v => v.listing_id));
          console.log(`Found video at index: ${videoIndex}`);
          if (videoIndex !== -1) {
            setCurrentVideoIndex(videoIndex);
            console.log(`Set current video index to: ${videoIndex}`);
          } else {
            // If specific video was requested but not found, set to 0 (first video)
            setCurrentVideoIndex(0);
            console.log('Specific video not found, set to index 0');
          }
        }
      } else {
        // Append new videos to existing list
        setVideos(prevVideos => [...prevVideos, ...validVideos]);
        
        // Load like statuses for new videos only
        loadLikeStatuses(validVideos);
      }
      
      // Update engagement counts
      const counts = {};
      validVideos.forEach(video => {
        counts[video.listing_id] = {
          views: video.view_count || 0,
          likes: video.like_count || 0,
          loves: video.love_count || 0,
          clicks: video.click_count || 0
        };
      });
      setEngagementCounts(prevCounts => ({ ...prevCounts, ...counts }));
      
      if (validVideos.length === 0 && isInitialLoad) {
        toast.error('No videos available');
      }
    } catch (error) {
      console.error('Error loading videos:', error);
      toast.error('Failed to load videos');
    } finally {
      if (isInitialLoad) {
        setLoading(false);
      }
    }
  };

  const loadRelatedProducts = async (providerId) => {
    if (!providerId) return;
    
    try {
      // This would typically call an API to get related products
      // For now, we'll use empty array
      setRelatedProducts([]);
    } catch (error) {
      console.error('Error loading related products:', error);
      setRelatedProducts([]);
    }
  };

  const trackEngagement = async (listingId, action) => {
    if (!listingId) return;
    
    try {
      await watchAPI.engage(listingId, action);
    } catch (error) {
      console.error(`Error tracking ${action}:`, error);
    }
  };

  const handleVideoClick = () => {
    const video = videoRefs[currentVideoIndex];
    if (video) {
      if (video.paused) {
        video.play();
        setIsPlaying(true);
      } else {
        video.pause();
        setIsPlaying(false);
      }
    }
  };

  const handleMuteToggle = () => {
    const video = videoRefs[currentVideoIndex];
    if (video) {
      video.muted = !video.muted;
      setIsMuted(video.muted);
    }
  };

  const handleEngagement = async (action) => {
    const currentVideo = videos[currentVideoIndex];
    if (currentVideo) {
      await trackEngagement(currentVideo.listing_id, action);
    }
  };

  const handleLike = async (listingId) => {
    console.log('Like button clicked for listing:', listingId);
    
    // Check if user is authenticated
    if (!isAuthenticated) {
      toast.error('Please log in to like videos');
      return;
    }
    
    try {
      const isCurrentlyLiked = likedItems[listingId];
      
      // Optimistically update UI
      setLikedItems(prev => ({
        ...prev,
        [listingId]: !isCurrentlyLiked
      }));
      
      // Call the appropriate API endpoint
      let response;
      if (isCurrentlyLiked) {
        response = await watchAPI.unlike(listingId);
      } else {
        response = await watchAPI.like(listingId);
      }
      
      // Update count with actual response from server
      setEngagementCounts(prev => ({
        ...prev,
        [listingId]: {
          ...prev[listingId],
          likes: response.like_count || 0
        }
      }));
      
      toast.success(isCurrentlyLiked ? 'Removed from likes' : 'Added to likes');
    } catch (error) {
      console.error('Error toggling like:', error);
      
      // Revert optimistic update on error
      setLikedItems(prev => ({
        ...prev,
        [listingId]: !prev[listingId]
      }));
      
      // Handle different error types
      if (error.response?.status === 401) {
        toast.error('Please log in to like videos');
      } else if (error.response?.status === 500) {
        toast.error('Server error. Please try again.');
      } else {
        toast.error('Failed to update like');
      }
    }
  };

  const loadLikeStatuses = async (videos) => {
    // Only load like statuses if user is authenticated
    if (!isAuthenticated) {
      console.log('User not authenticated, skipping like status loading');
      return;
    }
    
    console.log('Loading like statuses for videos:', videos.map(v => v.listing_id));
    try {
      const likePromises = videos.map(async (video) => {
        try {
          const response = await watchAPI.getLikeStatus(video.listing_id);
          return {
            listingId: video.listing_id,
            liked: response.liked,
            likeCount: response.like_count
          };
        } catch (error) {
          console.error(`Error loading like status for ${video.listing_id}:`, error);
          return {
            listingId: video.listing_id,
            liked: false,
            likeCount: video.like_count || 0
          };
        }
      });

      const likeStatuses = await Promise.all(likePromises);
      
      // Update liked items state
      const newLikedItems = {};
      const newEngagementCounts = {};
      
      likeStatuses.forEach(status => {
        newLikedItems[status.listingId] = status.liked;
        newEngagementCounts[status.listingId] = {
          ...engagementCounts[status.listingId],
          likes: status.likeCount
        };
      });
      
      setLikedItems(prev => ({ ...prev, ...newLikedItems }));
      setEngagementCounts(prev => ({ ...prev, ...newEngagementCounts }));
      
      console.log('Loaded like statuses:', likeStatuses);
    } catch (error) {
      console.error('Error loading like statuses:', error);
    }
  };

  const handleShare = () => {
    console.log('Share button clicked');
    setShowShareModal(true);
  };

  const handleStore = () => {
    console.log('Store button clicked');
    setShowStoreModal(true);
  };

  const handleProductClick = (product, e) => {
    if (e) {
      e.stopPropagation();
    }
    console.log('Product clicked:', product);
    setSelectedProductData(product);
    setShowProductModal(true);
  };

  const formatCount = (count) => {
    if (count >= 1000000) {
      return (count / 1000000).toFixed(1) + 'M';
    } else if (count >= 1000) {
      return (count / 1000).toFixed(1) + 'k';
    }
    return count.toString();
  };

  const handleSwipeUp = async () => {
    if (!isTransitioning) {
      setIsTransitioning(true);
      
      // If we're at the last video, try to load more
      if (currentVideoIndex >= videos.length - 1) {
        try {
          await loadVideos(videos.length, 5, false);
        } catch (error) {
          console.error('Error loading more videos:', error);
        }
      }
      
      // Move to next video if available
      if (currentVideoIndex < videos.length - 1) {
        setCurrentVideoIndex(currentVideoIndex + 1);
      } else {
        // Show end message
        toast.error('No more videos available');
      }
      
      setTimeout(() => setIsTransitioning(false), 300);
    }
  };

  const handleSwipeDown = () => {
    if (!isTransitioning) {
      setIsTransitioning(true);
      
      if (currentVideoIndex > 0) {
        setCurrentVideoIndex(currentVideoIndex - 1);
      } else {
        // Already at beginning, stay there or show message
        toast.error('You\'re at the beginning of the feed');
      }
      
      setTimeout(() => setIsTransitioning(false), 300);
    }
  };

  const handleTouchStart = (e) => {
    setTouchStart(e.targetTouches[0].clientY);
  };

  const handleTouchMove = (e) => {
    setTouchEnd(e.targetTouches[0].clientY);
  };

  const handleTouchEnd = () => {
    if (!touchStart || !touchEnd) return;
    
    const distance = touchStart - touchEnd;
    const isUpSwipe = distance > 50;
    const isDownSwipe = distance < -50;

    if (isUpSwipe) {
      handleSwipeUp();
    } else if (isDownSwipe) {
      handleSwipeDown();
    }
  };

  if (loading) {
    return (
      <div className="h-screen bg-black flex items-center justify-center">
        <div className="text-white text-xl">Loading videos...</div>
      </div>
    );
  }

  if (videos.length === 0) {
    return (
      <div className="h-screen bg-black flex items-center justify-center">
        <div className="text-center">
          <div className="text-white text-xl mb-4">No videos available</div>
          <button 
            onClick={() => loadVideos(0, 5, true)}
            className="bg-purple-600 text-white px-6 py-2 rounded-full"
          >
            Refresh
          </button>
        </div>
      </div>
    );
  }

  return (
      <div 
      className="h-screen bg-black overflow-hidden relative"
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      ref={videoContainerRef}
    >
      {/* Videos Container with Transform */}
      <div 
        className="relative h-full transition-transform duration-300 ease-out"
        style={{ 
          transform: `translateY(-${(currentVideoIndex + 1) * 100}vh)`,
          height: `${(videos.length + 3) * 100}vh`
        }}
      >
        {/* Beginning of Feed Indicator */}
        <div 
          className="absolute inset-0 w-full h-screen flex items-center justify-center bg-gradient-to-br from-purple-900 to-black"
          style={{ top: '0vh' }}
        >
          <div className="text-center text-white">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-2xl font-bold mb-2">You're at the beginning!</h3>
            <p className="text-gray-300 mb-6">Swipe up to see more videos</p>
            <button 
              onClick={() => {
                setCurrentVideoIndex(0);
                loadVideos(0, 5, true);
              }}
              className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-3 rounded-full font-semibold transition-colors"
            >
              Refresh Feed
            </button>
          </div>
        </div>

        {videos.map((video, index) => (
          <div
            key={video.listing_id}
            className="absolute inset-0 w-full h-screen"
            style={{ top: `${(index + 1) * 100}vh` }}
          >
            {/* Video Background with Blur */}
            {video.thumbnail_url && (
              <div 
                className="absolute inset-0 bg-cover bg-center"
                style={{
                  backgroundImage: `url(${video.thumbnail_url})`,
                  filter: 'blur(20px)',
                  transform: 'scale(1.1)'
                }}
              />
            )}

            {/* Dark Overlay */}
            <div className="absolute inset-0 bg-black bg-opacity-40" />

            {/* Main Video Container */}
            <div 
              className="relative h-full flex items-center justify-center"
              onClick={handleVideoClick}
            >
              {video.video_url ? (
                <video
                  ref={setVideoRef(index)}
                  className="h-full w-full object-cover"
                  src={video.video_url}
                  muted={isMuted}
                  loop
                  playsInline
                  preload="metadata"
                  onLoadedData={() => {
                    if (index === currentVideoIndex) {
                      const videoEl = videoRefs[index];
                      if (videoEl) {
                        videoEl.play().catch(console.error);
                        setIsPlaying(true);
                      }
                    }
                  }}
                  onError={(e) => {
                    console.error('Video error:', e);
                    console.error('Video src:', video.video_url);
                    toast.error(`Video failed to load: ${video.video_url}`);
                  }}
                  onCanPlay={() => {
                    if (index === currentVideoIndex) {
                      const videoEl = videoRefs[index];
                      if (videoEl) {
                        videoEl.play().catch(console.error);
                        setIsPlaying(true);
                      }
                    }
                  }}
                />
              ) : (
                /* Fallback when no video URL */
                <div className="h-full w-full flex items-center justify-center bg-gradient-to-br from-purple-900 to-black">
                  <div className="text-center">
                    <div className="w-24 h-24 bg-white bg-opacity-20 rounded-full flex items-center justify-center mx-auto mb-4">
                      <Play className="w-12 h-12 text-white" />
                    </div>
                    <p className="text-white text-lg font-medium">No video available</p>
                    <p className="text-gray-400 text-sm mt-2">This listing doesn't have a video</p>
                  </div>
                </div>
              )}

              {/* Video Controls Overlay */}
              <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                {!isPlaying && video.video_url && index === currentVideoIndex && (
                  <div className="bg-black bg-opacity-60 rounded-full p-6 pointer-events-auto">
                    <Play className="w-16 h-16 text-white" />
                  </div>
                )}
              </div>

              {/* Mute Button */}
              {index === currentVideoIndex && (
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleMuteToggle();
                  }}
                  className="absolute top-6 right-6 bg-black bg-opacity-60 rounded-full p-3 backdrop-blur-sm z-10"
                >
                  {isMuted ? (
                    <VolumeX className="w-6 h-6 text-white" />
                  ) : (
                    <Volume2 className="w-6 h-6 text-white" />
                  )}
                </button>
              )}
            </div>

            {/* Engagement Bar (Right Side) */}
            {index === currentVideoIndex && (
              <div className="absolute right-4 top-1/3 flex flex-col items-center space-y-4 z-30">
                {/* Views */}
                <div className="flex flex-col items-center">
                  <div className="bg-black bg-opacity-60 rounded-full p-3 backdrop-blur-sm">
                    <Eye className="w-6 h-6 text-white" />
                  </div>
                  <span className="text-white text-sm font-medium mt-1">
                    {formatCount(engagementCounts[video.listing_id]?.views || 0)}
                  </span>
                </div>

                {/* Likes */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleLike(video.listing_id);
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                >
                  <div className="bg-black bg-opacity-60 rounded-full p-3 backdrop-blur-sm group-active:scale-95 transition-transform">
                    <Heart className={`w-6 h-6 ${likedItems[video.listing_id] ? 'text-red-500 fill-current' : 'text-white'}`} />
                  </div>
                  <span className="text-white text-sm font-medium mt-1">
                    {formatCount(engagementCounts[video.listing_id]?.likes || 0)}
                  </span>
                </button>

                {/* Sparkles/Magic */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleEngagement('love');
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                >
                  <div className="bg-black bg-opacity-60 rounded-full p-3 backdrop-blur-sm group-active:scale-95 transition-transform">
                    <Sparkles className="w-6 h-6 text-yellow-400" />
                  </div>
                  <span className="text-white text-sm font-medium mt-1">
                    {formatCount(engagementCounts[video.listing_id]?.loves || 0)}
                  </span>
                </button>

                {/* Share */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleShare();
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                >
                  <div className="bg-black bg-opacity-60 rounded-full p-3 backdrop-blur-sm group-active:scale-95 transition-transform">
                    <Share2 className="w-6 h-6 text-white" />
                  </div>
                  <span className="text-white text-sm font-medium mt-1">
                    {formatCount(engagementCounts[video.listing_id]?.clicks || 0)}
                  </span>
                </button>

                {/* Cart/Shop */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleStore();
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                >
                  <div className="bg-black bg-opacity-60 rounded-full p-3 backdrop-blur-sm group-active:scale-95 transition-transform">
                    <ShoppingCart className="w-6 h-6 text-white" />
                  </div>
                  <span className="text-white text-sm font-medium mt-1">
                    Store
                  </span>
                </button>
              </div>
            )}

            {/* Bottom Content Area - Provider Info + Product Carousel */}
            {index === currentVideoIndex && (
              <div className="absolute bottom-20 left-4 right-20">
                {/* Provider Info Section */}
                <div className="mb-4">
                  <div className="flex items-center space-x-3">
                    {/* Avatar/Shop Icon */}
                    <div className="w-12 h-12 bg-blue-500 rounded-full flex items-center justify-center shadow-lg flex-shrink-0">
                      <ShoppingCart className="w-6 h-6 text-white" />
                    </div>

                    {/* Info */}
                    <div className="flex flex-col min-w-0 flex-1">
                      {/* Influencer Badge and Username */}
                      <div className="flex items-center space-x-2 mb-1">
                        <span className="bg-orange-500 text-white text-xs px-2 py-1 rounded-full font-medium flex-shrink-0">
                          {video.provider_type === 'INFLUENCER' ? 'Influencer' : 'Brand'}
                        </span>
                        <span className="text-white text-sm font-semibold truncate">
                          @{video.handle_address || video.provider_name?.toLowerCase() || 'user'}
                        </span>
                        <div className="w-4 h-4 bg-blue-500 rounded-full flex items-center justify-center flex-shrink-0">
                          <span className="text-white text-xs font-bold"></span>
                        </div>
                      </div>

                      {/* Product Name */}
                      <span className="text-white font-bold text-base truncate">
                        {video.product_name}
                      </span>
                    </div>
                  </div>
                </div>

                {/* Product Carousel Section */}
                <div className="flex gap-3 overflow-x-auto scrollbar-hide">
                  {/* Current Product (First Card) */}
                  <div
                    onClick={(e) => handleProductClick(video, e)}
                    className="product-card flex-shrink-0 w-24 h-24 rounded-xl border-2 border-orange-500 shadow-lg shadow-orange-500/30 overflow-hidden cursor-pointer transition-all duration-200"
                  >
                    <div className="w-full h-full relative">
                      {video.thumbnail_url ? (
                        <img 
                          src={video.thumbnail_url} 
                          alt={video.product_name}
                          className="w-full h-full object-cover"
                          onError={(e) => {
                            e.target.style.display = 'none';
                            e.target.nextSibling.style.display = 'flex';
                          }}
                        />
                      ) : null}
                      <div 
                        className={`w-full h-full bg-gradient-to-br from-gray-700 to-gray-900 flex items-center justify-center ${video.thumbnail_url ? 'hidden' : 'flex'}`}
                      >
                        <span className="text-white text-xs font-bold text-center px-1">
                          {video.product_name || 'Current Product'}
                        </span>
                      </div>
                    </div>
                  </div>

                  {/* Related Products */}
                  {relatedProducts.map((product, productIndex) => (
                    <div
                      key={product.listing_id}
                      onClick={(e) => handleProductClick(product, e)}
                      className="product-card flex-shrink-0 w-24 h-24 rounded-xl border-2 border-white border-opacity-30 overflow-hidden cursor-pointer transition-all duration-200 hover:border-opacity-60"
                    >
                      <div className="w-full h-full relative">
                        {product.thumbnail_url ? (
                          <img 
                            src={product.thumbnail_url} 
                            alt={product.product_name}
                            className="w-full h-full object-cover"
                            onError={(e) => {
                              e.target.style.display = 'none';
                              e.target.nextSibling.style.display = 'flex';
                            }}
                          />
                        ) : null}
                        <div 
                          className={`w-full h-full bg-gradient-to-br from-gray-700 to-gray-900 flex items-center justify-center ${product.thumbnail_url ? 'hidden' : 'flex'}`}
                        >
                          <span className="text-white text-xs font-bold text-center px-1">
                            {product.product_name || 'Product'}
                          </span>
                        </div>
                      </div>
                    </div>
                  ))}
                      </div>
                    </div>
                  )}
                </div>
        ))}
        
        {/* End of Feed Indicator */}
        <div 
          className="absolute inset-0 w-full h-screen flex items-center justify-center bg-gradient-to-br from-gray-800 to-black"
          style={{ top: `${(videos.length + 1) * 100}vh` }}
        >
          <div className="text-center text-white">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-2xl font-bold mb-2">You've reached the end!</h3>
            <p className="text-gray-300 mb-6">No more videos available</p>
            <button 
              onClick={() => {
                setCurrentVideoIndex(0);
                loadVideos(0, 5, true);
              }}
              className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-3 rounded-full font-semibold transition-colors"
            >
              Start Over
            </button>
              </div>
          </div>
      </div>

      {/* Swipe Indicators */}
      <div className="absolute left-1/2 transform -translate-x-1/2 top-8 flex flex-col items-center space-y-2 z-10">
        {currentVideoIndex > 0 ? (
          <div className="bg-black bg-opacity-60 rounded-full p-2 backdrop-blur-sm">
            <ChevronLeft className="w-4 h-4 text-white rotate-90" />
          </div>
        ) : (
          <div className="bg-purple-500 bg-opacity-80 rounded-full p-2 backdrop-blur-sm">
            <span className="text-white text-xs font-bold">START</span>
          </div>
        )}
        
        {currentVideoIndex < videos.length - 1 ? (
          <div className="bg-black bg-opacity-60 rounded-full p-2 backdrop-blur-sm">
            <ChevronRight className="w-4 h-4 text-white rotate-90" />
          </div>
        ) : (
          <div className="bg-red-500 bg-opacity-80 rounded-full p-2 backdrop-blur-sm">
            <span className="text-white text-xs font-bold">END</span>
          </div>
        )}
      </div>

      {/* Keyboard Support for Desktop */}
      <div 
        className="absolute inset-0 focus:outline-none" 
        tabIndex={0}
        onKeyDown={(e) => {
          if (e.key === 'ArrowUp') {
            handleSwipeUp();
          } else if (e.key === 'ArrowDown') {
            handleSwipeDown();
          }
        }}
      />

      {/* Product Modal */}
      {showProductModal && selectedProductData && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-center justify-center z-[10001] p-4">
          <div className="bg-white rounded-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
            {/* Header */}
            <div className="flex items-center justify-between p-6 border-b border-gray-200">
              <h2 className="text-xl font-bold text-gray-900">
                Product Details
              </h2>
              <button
                onClick={() => setShowProductModal(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            {/* Content */}
            <div className="p-6">
              {/* Product Image */}
              {selectedProductData.thumbnail_url && (
                <div className="mb-4">
                  <img 
                    src={selectedProductData.thumbnail_url} 
                    alt={selectedProductData.product_name}
                    className="w-full h-48 object-cover rounded-lg"
                  />
                  </div>
              )}

              {/* Product Info */}
              <div className="space-y-4">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">
                    {selectedProductData.product_name}
                  </h3>
                  <p className="text-gray-600">
                    by {selectedProductData.provider_name || selectedProductData.handle_address}
                  </p>
                </div>

                {selectedProductData.price && (
                  <div className="text-2xl font-bold text-gray-900">
                      {selectedProductData.price}
                  </div>
                )}

                {/* Description */}
                {selectedProductData.description && (
                  <div>
                    <h5 className="font-semibold text-gray-900 mb-2">Description</h5>
                    <p className="text-gray-600 text-sm leading-relaxed">
                      {selectedProductData.description}
                    </p>
                  </div>
                )}

                {/* Engagement Stats */}
                <div className="grid grid-cols-2 gap-4 pt-4 border-t border-gray-200">
                  <div className="text-center">
                    <div className="text-2xl font-bold text-gray-900">
                      {formatCount(selectedProductData.view_count || 0)}
                    </div>
                    <div className="text-xs text-gray-500 uppercase tracking-wide">Views</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-gray-900">
                      {formatCount(selectedProductData.like_count || 0)}
                    </div>
                    <div className="text-xs text-gray-500 uppercase tracking-wide">Likes</div>
                  </div>
                </div>
              </div>
            </div>

            {/* Footer */}
            <div className="p-6 border-t border-gray-200 bg-gray-50">
              {selectedProductData.link ? (
                <a
                  href={selectedProductData.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="w-full bg-orange-500 text-white py-3 px-4 rounded-xl font-bold text-center block hover:bg-orange-600 transition-colors shadow-lg"
                >
                  View Product
                </a>
              ) : (
                <div className="w-full bg-gray-300 text-gray-500 py-3 px-4 rounded-xl font-semibold text-center">
                  No link available
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Share Modal */}
      {showShareModal && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-end justify-center z-[10001] animate-fadeIn">
          <div 
            className="bg-black w-full max-w-sm rounded-t-3xl shadow-2xl animate-slideUp overflow-hidden"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Handle bar */}
            <div className="flex justify-center pt-3 pb-2">
              <div className="w-12 h-1 bg-gray-600 rounded-full"></div>
            </div>

            {/* Header */}
            <div className="px-6 py-4 border-b border-gray-700">
              <h3 className="text-white text-xl font-bold text-center">Share</h3>
            </div>

            {/* Share Options */}
            <div className="p-6 space-y-4">
              {/* Copy Link */}
              <button
                onClick={async () => {
                  try {
                    const currentVideo = videos[currentVideoIndex];
                    const shareUrl = `${window.location.origin}/watch?video=${currentVideo?.listing_id}`;
                    await navigator.clipboard.writeText(shareUrl);
                    toast.success('Link copied to clipboard!');
                    setShowShareModal(false);
                  } catch (error) {
                    toast.error('Failed to copy link');
                  }
                }}
                className="w-full bg-purple-600 hover:bg-purple-700 text-white py-4 px-6 rounded-xl font-semibold transition-colors flex items-center justify-center space-x-3"
              >
                <Share2 className="w-5 h-5" />
                <span>Copy Link</span>
              </button>

              {/* Share to Social Media */}
              <div className="grid grid-cols-2 gap-3">
                <button className="bg-blue-600 hover:bg-blue-700 text-white py-3 px-4 rounded-xl font-medium transition-colors">
                  WhatsApp
                </button>
                <button className="bg-gray-700 hover:bg-gray-600 text-white py-3 px-4 rounded-xl font-medium transition-colors">
                  More
                </button>
              </div>

              {/* Product Info */}
              <div className="bg-gray-800 rounded-xl p-4 mt-4">
                <div className="flex items-center space-x-3">
                  {videos[currentVideoIndex]?.thumbnail_url && (
                    <img 
                      src={videos[currentVideoIndex].thumbnail_url} 
                      alt={videos[currentVideoIndex].product_name}
                      className="w-12 h-12 rounded-lg object-cover"
                    />
                  )}
                  <div className="flex-1">
                    <p className="text-white font-semibold text-sm">
                      {videos[currentVideoIndex]?.product_name}
                    </p>
                    <p className="text-gray-400 text-xs">
                      by @{videos[currentVideoIndex]?.handle_address}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Store Modal */}
      {showStoreModal && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-end justify-center z-[10001] animate-fadeIn">
          <div 
            className="bg-black w-full max-w-sm rounded-t-3xl shadow-2xl animate-slideUp overflow-hidden max-h-[80vh]"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Handle bar for dragging */}
            <div 
              className="flex justify-center pt-3 pb-2 cursor-grab active:cursor-grabbing"
              onMouseDown={(e) => {
                const modal = e.currentTarget.closest('.bg-black');
                let startY = e.clientY;
                let startHeight = modal.offsetHeight;
                
                const handleMouseMove = (e) => {
                  const deltaY = startY - e.clientY;
                  const newHeight = startHeight + deltaY;
                  const maxHeight = window.innerHeight * 0.8;
                  const minHeight = 200;
                  
                  if (newHeight >= minHeight && newHeight <= maxHeight) {
                    modal.style.height = `${newHeight}px`;
                  }
                };
                
                const handleMouseUp = () => {
                  const modal = e.currentTarget.closest('.bg-black');
                  const currentHeight = modal.offsetHeight;
                  const threshold = window.innerHeight * 0.3;
                  
                  if (currentHeight < threshold) {
                    setShowStoreModal(false);
                  } else {
                    modal.style.height = '80vh';
                  }
                  
                  document.removeEventListener('mousemove', handleMouseMove);
                  document.removeEventListener('mouseup', handleMouseUp);
                };
                
                document.addEventListener('mousemove', handleMouseMove);
                document.addEventListener('mouseup', handleMouseUp);
              }}
            >
              <div className="w-12 h-1 bg-gray-600 rounded-full"></div>
            </div>

            {/* Header */}
            <div className="px-6 py-4 border-b border-gray-700">
              <div className="flex items-center justify-between">
                <h3 className="text-white text-xl font-bold">Store</h3>
                <button
                  onClick={() => setShowStoreModal(false)}
                  className="text-gray-400 hover:text-white transition-colors"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
            </div>

            {/* Store Content */}
            <div className="p-6 space-y-4 overflow-y-auto max-h-[60vh]">
              {/* Current Product */}
              {videos[currentVideoIndex] && (
                <div className="bg-gray-800 rounded-xl p-4">
                  <div className="flex items-center space-x-3">
                    {videos[currentVideoIndex].thumbnail_url && (
                      <img 
                        src={videos[currentVideoIndex].thumbnail_url} 
                        alt={videos[currentVideoIndex].product_name}
                        className="w-16 h-16 rounded-lg object-cover"
                      />
                    )}
                    <div className="flex-1">
                      <h4 className="text-white font-semibold">
                        {videos[currentVideoIndex].product_name}
                      </h4>
                      <p className="text-gray-400 text-sm">
                        by @{videos[currentVideoIndex].handle_address}
                      </p>
                      <p className="text-purple-400 font-semibold mt-1">
                        Featured Product
                      </p>
                    </div>
                  </div>
                </div>
              )}

              {/* Related Products */}
              <div>
                <h4 className="text-white font-semibold mb-3">More from this Store</h4>
                <div className="space-y-3">
                  {relatedProducts.length > 0 ? (
                    relatedProducts.slice(0, 5).map((product) => (
                      <div key={product.listing_id} className="bg-gray-800 rounded-xl p-4">
                        <div className="flex items-center space-x-3">
                          {product.thumbnail_url && (
                            <img 
                              src={product.thumbnail_url} 
                              alt={product.product_name}
                              className="w-12 h-12 rounded-lg object-cover"
                            />
                          )}
                          <div className="flex-1">
                            <h5 className="text-white font-medium text-sm">
                              {product.product_name}
                            </h5>
                            <p className="text-gray-400 text-xs">
                              {product.category_name || 'Product'}
                            </p>
                          </div>
                          <button className="bg-purple-600 hover:bg-purple-700 text-white px-3 py-1 rounded-lg text-xs font-medium transition-colors">
                            View
                          </button>
                        </div>
                      </div>
                    ))
                  ) : (
                    <div className="text-center py-8">
                      <p className="text-gray-400">No other products available</p>
                    </div>
                  )}
                </div>
              </div>

              {/* Store Info */}
              <div className="bg-gray-800 rounded-xl p-4">
                <h4 className="text-white font-semibold mb-2">Store Information</h4>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-400">Provider:</span>
                    <span className="text-white">@{videos[currentVideoIndex]?.handle_address}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-400">Type:</span>
                    <span className="text-white">{videos[currentVideoIndex]?.provider_type}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-400">Products:</span>
                    <span className="text-white">{relatedProducts.length + 1}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Watch;