import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Plus, Package, Briefcase, Eye, Heart, MousePointer, Loader, ArrowLeft } from 'lucide-react';
import { listingAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import Header from '../components/Header';

const MyListings = () => {
  const navigate = useNavigate();
  const [listings, setListings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('ALL');

  useEffect(() => {
    loadListings();
  }, []);

  const loadListings = async () => {
    try {
      setLoading(true);
      const data = await listingAPI.getMy();
      setListings(data.listings || []);
    } catch (error) {
      console.error('Error loading listings:', error);
      toast.error('Failed to load listings');
    } finally {
      setLoading(false);
    }
  };

  const filteredListings = listings.filter(listing => 
    filter === 'ALL' || listing.type === filter
  );

  const stats = {
    total: listings.length,
    products: listings.filter(l => l.type === 'PRODUCT').length,
    services: listings.filter(l => l.type === 'SERVICE').length,
    totalViews: listings.reduce((sum, l) => sum + (l.view_count || 0), 0),
    totalLoves: listings.reduce((sum, l) => sum + (l.love_count || 0), 0),
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black star-particles">
        <Loader className="w-8 h-8 animate-spin text-purple-500" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      
      <div className="pt-16 pb-24 px-4 relative z-10">
        {/* Header */}
        <div className="max-w-7xl mx-auto mb-6">
          <div className="flex items-center justify-between mb-4">
            <button
              onClick={() => navigate('/listing')}
              className="flex items-center gap-2 text-gray-400 hover:text-white transition-colors font-inter"
            >
              <ArrowLeft className="w-5 h-5" />
              Back
            </button>
            <button
              onClick={() => navigate('/listing')}
              className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-3 rounded-xl font-semibold transition-all flex items-center gap-2 shadow-lg font-inter"
            >
              <Plus className="w-5 h-5" />
              New Listing
            </button>
          </div>
          
          <h1 className="text-3xl font-bold text-white font-inter">My Listings</h1>
          <p className="text-gray-300 text-sm mt-1 font-inter">
            {stats.total} total listing{stats.total !== 1 ? 's' : ''}
          </p>
        </div>

        <div className="max-w-7xl mx-auto">
          {/* Stats Cards */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
            <div className="bg-gray-800 border border-gray-700 rounded-xl p-4 shadow-sm">
              <div className="text-2xl font-bold text-white font-inter">{stats.total}</div>
              <div className="text-sm text-gray-400 font-inter">Total Listings</div>
            </div>
            <div className="bg-gray-800 border border-gray-700 rounded-xl p-4 shadow-sm">
              <div className="text-2xl font-bold text-purple-500 font-inter">{stats.products}</div>
              <div className="text-sm text-gray-400 font-inter">Products</div>
            </div>
            <div className="bg-gray-800 border border-gray-700 rounded-xl p-4 shadow-sm">
              <div className="text-2xl font-bold text-gray-400 font-inter">{stats.services}</div>
              <div className="text-sm text-gray-400 font-inter">Services</div>
            </div>
            <div className="bg-gray-800 border border-gray-700 rounded-xl p-4 shadow-sm">
              <div className="text-2xl font-bold text-purple-500 font-inter">{stats.totalViews}</div>
              <div className="text-sm text-gray-400 font-inter">Total Views</div>
            </div>
          </div>

          {/* Filter Tabs */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-2 mb-6 flex gap-2">
            {['ALL', 'PRODUCT', 'SERVICE'].map(tab => (
              <button
                key={tab}
                onClick={() => setFilter(tab)}
                className={`flex-1 py-2 px-4 rounded-lg font-medium transition-all font-inter ${
                  filter === tab
                    ? 'bg-purple-600 text-white'
                    : 'text-gray-400 hover:bg-gray-700'
                }`}
              >
                {tab === 'ALL' ? 'All' : tab === 'PRODUCT' ? 'Products' : 'Services'}
              </button>
            ))}
          </div>

          {/* Listings Grid */}
          {filteredListings.length === 0 ? (
            <div className="bg-gray-800 border border-gray-700 rounded-2xl shadow-sm p-12 text-center">
              <div className="w-20 h-20 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-4">
                <Package className="w-10 h-10 text-white" />
              </div>
              <h3 className="text-xl font-semibold text-white mb-2 font-inter">No listings yet</h3>
              <p className="text-gray-400 mb-6 font-inter">Start by creating your first listing</p>
              <button
                onClick={() => navigate('/listing')}
                className="bg-gradient-to-r from-purple-600 to-purple-700 text-white px-8 py-3 rounded-xl font-semibold hover:from-purple-700 hover:to-purple-800 transition-all inline-flex items-center gap-2 shadow-lg font-inter"
              >
                <Plus className="w-5 h-5" />
                Create Listing
              </button>
            </div>
          ) : (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredListings.map(listing => (
                <div
                  key={listing.listing_id}
                  className="bg-gray-800 border border-gray-700 rounded-2xl shadow-sm hover:shadow-lg transition-all overflow-hidden group cursor-pointer"
                  onClick={() => navigate(`/listing/${listing.listing_id}`)}
                >
                  {/* Thumbnail */}
                  <div className="relative h-48 bg-gray-700">
                    {listing.thumbnail_url ? (
                      <img
                        src={listing.thumbnail_url}
                        alt={listing.product_name}
                        className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-purple-600 to-purple-800">
                        {listing.type === 'PRODUCT' ? (
                          <Package className="w-16 h-16 text-white" />
                        ) : (
                          <Briefcase className="w-16 h-16 text-white" />
                        )}
                      </div>
                    )}
                    <div className="absolute top-3 right-3 bg-black/90 backdrop-blur-sm px-3 py-1 rounded-full text-sm font-medium font-inter text-white">
                      {listing.type === 'PRODUCT' ? ' Product' : ' Service'}
                    </div>
                  </div>

                  {/* Content */}
                  <div className="p-4">
                    <h3 className="font-bold text-lg text-white mb-1 line-clamp-1 font-inter">
                      {listing.product_name}
                    </h3>
                    <p className="text-purple-400 font-bold text-xl mb-2 font-inter">
                      {Number(listing.price).toLocaleString()}
                    </p>
                    <p className="text-gray-400 text-sm line-clamp-2 mb-3 font-inter">
                      {listing.description}
                    </p>

                    {/* Stats */}
                    <div className="flex items-center gap-4 text-sm text-gray-400">
                      <div className="flex items-center gap-1">
                        <Eye className="w-4 h-4" />
                        <span className="font-inter">{listing.view_count || 0}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <Heart className="w-4 h-4" />
                        <span className="font-inter">{listing.love_count || 0}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <MousePointer className="w-4 h-4" />
                        <span className="font-inter">{listing.click_count || 0}</span>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

    </div>
  );
};

export default MyListings;
