import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Store, User, Phone, MapPin, Building, FileText, Home as HomeIcon, AtSign, Loader, ArrowLeft } from 'lucide-react';
import { providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import Header from '../components/Header';

const ProviderRegistration = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    location: '',
    provider_type: 'BRAND',
    business_name: '',
    gst: '',
    business_address: '',
    handle_address: ''
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      await providerAPI.create(formData);
      toast.success(' Provider profile created successfully!');
      navigate('/listing');
    } catch (error) {
      console.error('Error creating provider:', error);
      toast.error(error.response?.data?.error || 'Failed to create provider profile');
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      
      <div className="pt-16 pb-24 px-4 max-w-4xl mx-auto">
        {/* Back Button */}
        <button
          onClick={() => navigate('/')}
          className="flex items-center gap-2 text-gray-300 hover:text-white mb-6 transition-colors"
        >
          <ArrowLeft className="w-5 h-5" />
          <span className="font-inter">Back to Home</span>
        </button>

        {/* Header Section */}
        <div className="text-center mb-8">
          <div className="w-20 h-20 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-4 shadow-lg">
            <Store className="w-10 h-10 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-white mb-2 font-inter">Become a Provider</h1>
          <p className="text-gray-400 font-inter">Create your profile to start listing products and services</p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Provider Type Selection */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-6">
            <h2 className="text-xl font-bold text-white mb-4 font-inter">I am a...</h2>
            <div className="grid md:grid-cols-2 gap-4">
              <button
                type="button"
                onClick={() => setFormData({ ...formData, provider_type: 'BRAND' })}
                className={`p-6 rounded-xl border-2 transition-all ${
                  formData.provider_type === 'BRAND'
                    ? 'border-purple-500 bg-purple-900/50'
                    : 'border-gray-600 hover:border-purple-400'
                }`}
              >
                <Store className={`w-12 h-12 mx-auto mb-3 ${
                  formData.provider_type === 'BRAND' ? 'text-purple-500' : 'text-gray-400'
                }`} />
                <h3 className="font-bold text-lg text-white mb-1 font-inter">Brand / Business</h3>
                <p className="text-sm text-gray-400 font-inter">I sell products or provide services</p>
              </button>
              
              <button
                type="button"
                onClick={() => setFormData({ ...formData, provider_type: 'INFLUENCER' })}
                className={`p-6 rounded-xl border-2 transition-all ${
                  formData.provider_type === 'INFLUENCER'
                    ? 'border-purple-500 bg-purple-900/50'
                    : 'border-gray-600 hover:border-purple-400'
                }`}
              >
                <User className={`w-12 h-12 mx-auto mb-3 ${
                  formData.provider_type === 'INFLUENCER' ? 'text-purple-500' : 'text-gray-400'
                }`} />
                <h3 className="font-bold text-lg text-white mb-1 font-inter">Influencer / Creator</h3>
                <p className="text-sm text-gray-400 font-inter">I promote and showcase products</p>
              </button>
            </div>
          </div>

          {/* Basic Information */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-6 space-y-5">
            <h2 className="text-xl font-bold text-white mb-4 font-inter">Basic Information</h2>
            
            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <User className="w-4 h-4 inline mr-2" />
                Full Name *
              </label>
              <input
                type="text"
                name="name"
                required
                value={formData.name}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="Enter your full name"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <Phone className="w-4 h-4 inline mr-2" />
                Phone Number *
              </label>
              <input
                type="tel"
                name="phone"
                required
                value={formData.phone}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="+91 9876543210"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <MapPin className="w-4 h-4 inline mr-2" />
                Location (City, State) *
              </label>
              <input
                type="text"
                name="location"
                required
                value={formData.location}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="Mumbai, Maharashtra"
              />
            </div>
          </div>

          {/* Business Information */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-6 space-y-5">
            <h2 className="text-xl font-bold text-white mb-4 font-inter">
              Business Information
              <span className="text-sm font-normal text-gray-400 ml-2">(Optional)</span>
            </h2>
            
            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <Building className="w-4 h-4 inline mr-2" />
                Business Name
              </label>
              <input
                type="text"
                name="business_name"
                value={formData.business_name}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="Your Business Name"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <FileText className="w-4 h-4 inline mr-2" />
                GST Number
              </label>
              <input
                type="text"
                name="gst"
                value={formData.gst}
                onChange={handleInputChange}
                maxLength={15}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="27ABCDE1234F1Z5"
              />
              <p className="text-xs text-gray-400 mt-1 font-inter">15-digit GST number (if registered)</p>
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <HomeIcon className="w-4 h-4 inline mr-2" />
                Business Address
              </label>
              <textarea
                name="business_address"
                value={formData.business_address}
                onChange={handleInputChange}
                rows={3}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="Complete business address with pincode"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                <AtSign className="w-4 h-4 inline mr-2" />
                Social Handle / Website
              </label>
              <input
                type="text"
                name="handle_address"
                value={formData.handle_address}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="@yourbrand or https://yourwebsite.com"
              />
              <p className="text-xs text-gray-400 mt-1 font-inter">Instagram handle, website, or social media link</p>
            </div>
          </div>

          {/* Benefits */}
          <div className="bg-gray-800 rounded-xl shadow-lg p-6 border-2 border-purple-500">
            <h3 className="text-xl font-bold mb-4 font-inter text-white"> What you'll get:</h3>
            <div className="grid md:grid-cols-2 gap-3">
              <div className="flex items-start gap-2">
                <div className="w-6 h-6 bg-purple-500 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                  
                </div>
                <p className="text-sm font-inter text-gray-300">Create unlimited product/service listings</p>
              </div>
              <div className="flex items-start gap-2">
                <div className="w-6 h-6 bg-purple-500 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5 text-white text-xs">
                  
                </div>
                <p className="text-sm font-inter text-gray-300">Reach thousands of potential customers</p>
              </div>
              <div className="flex items-start gap-2">
                <div className="w-6 h-6 bg-purple-500 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5 text-white text-xs">
                  
                </div>
                <p className="text-sm font-inter text-gray-300">Track engagement and analytics</p>
              </div>
              <div className="flex items-start gap-2">
                <div className="w-6 h-6 bg-purple-500 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5 text-white text-xs">
                  
                </div>
                <p className="text-sm font-inter text-gray-300">Build your brand presence</p>
              </div>
            </div>
          </div>

          {/* Submit Button */}
          <div className="pt-4">
            <button
              type="submit"
              disabled={loading}
              className="w-full bg-gradient-to-r from-purple-600 to-purple-700 text-white py-4 rounded-xl font-semibold hover:from-purple-700 hover:to-purple-800 transition-all disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2 text-lg shadow-lg font-inter"
            >
              {loading ? (
                <>
                  <Loader className="w-6 h-6 animate-spin" />
                  Creating Profile...
                </>
              ) : (
                <>
                  <Store className="w-6 h-6" />
                  Create Provider Profile
                </>
              )}
            </button>
            <p className="text-center text-sm text-gray-400 mt-3 font-inter">
              You can update these details later from your profile
            </p>
          </div>
        </form>
      </div>

    </div>
  );
};

export default ProviderRegistration;
