// QuickBubbles.jsx - Circular quick-access category bubbles
import React from 'react';

const QuickBubbles = ({ categories, onSelect }) => {
  const colors = ['bg-purple-600', 'bg-purple-500', 'bg-purple-700', 'bg-purple-400', 'bg-purple-800', 'bg-purple-300'];
  
  const bubbles = (categories || []).slice(0, 6).map((category, index) => ({
    id: category.category_id,
    title: category.category_name,
    thumb: category.image_url || `https://picsum.photos/64/64?random=${100 + index}`,
    color: colors[index % colors.length]
  }));

  return (
    <div className="px-4 py-4 bg-black star-particles">
      <div className="flex gap-4 overflow-x-auto pb-2 relative z-10" style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}>
        {bubbles.map((bubble) => (
          <button
            key={bubble.id}
            onClick={() => onSelect(bubble.title)}
            className="flex flex-col items-center gap-2 flex-shrink-0"
            aria-label={`Browse ${bubble.title}`}
          >
            <div className={`w-16 h-16 rounded-full ${bubble.color} flex items-center justify-center overflow-hidden shadow-md hover:scale-105 transition-transform border-2 border-white/20`}>
              <img
                src={bubble.thumb}
                alt={bubble.title}
                className="w-full h-full object-cover"
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.parentElement.innerHTML = `<span class="text-2xl text-white font-bold">${bubble.title.charAt(0)}</span>`;
                }}
              />
            </div>
            <span className="text-xs font-inter font-medium text-white">{bubble.title}</span>
          </button>
        ))}
      </div>
    </div>
  );
};

export default QuickBubbles;

