import React, { useState } from 'react';
import { X, Save, Loader } from 'lucide-react';
import { userAPI, providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

const EditProfileModal = ({ type, user, provider, onClose, onSuccess }) => {
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    // User fields
    name: user?.name || '',
    phone: user?.phone || '',
    
    // Provider fields
    business_name: provider?.business_name || '',
    type: provider?.type || 'brand',
    location: provider?.location || '',
    gst_number: provider?.gst_number || '',
    business_address: provider?.business_address || '',
    handle_address: provider?.handle_address || ''
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      if (type === 'user') {
        await userAPI.updateProfile({
          name: formData.name,
          phone: formData.phone
        });
        toast.success('Profile updated successfully!');
      } else if (type === 'provider') {
        await providerAPI.updateProfile({
          business_name: formData.business_name,
          type: formData.type,
          location: formData.location,
          gst_number: formData.gst_number,
          business_address: formData.business_address,
          handle_address: formData.handle_address
        });
        toast.success('Provider profile updated successfully!');
      }
      onSuccess();
    } catch (error) {
      console.error('Error updating profile:', error);
      toast.error(error.response?.data?.error || 'Failed to update profile');
    } finally {
      setLoading(false);
    }
  };

  const renderUserForm = () => (
    <div className="space-y-4">
      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Full Name *
        </label>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={handleInputChange}
          required
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          placeholder="Enter your full name"
        />
      </div>

      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Phone Number *
        </label>
        <input
          type="tel"
          name="phone"
          value={formData.phone}
          onChange={handleInputChange}
          required
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          placeholder="Enter your phone number"
        />
      </div>
    </div>
  );

  const renderProviderForm = () => (
    <div className="space-y-4">
      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Business Name *
        </label>
        <input
          type="text"
          name="business_name"
          value={formData.business_name}
          onChange={handleInputChange}
          required
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          placeholder="Enter your business name"
        />
      </div>

      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Provider Type *
        </label>
        <select
          name="type"
          value={formData.type}
          onChange={handleInputChange}
          required
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
        >
          <option value="brand">Brand</option>
          <option value="influencer">Influencer</option>
        </select>
      </div>

      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Location *
        </label>
        <input
          type="text"
          name="location"
          value={formData.location}
          onChange={handleInputChange}
          required
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          placeholder="Enter your location"
        />
      </div>

      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          GST Number
        </label>
        <input
          type="text"
          name="gst_number"
          value={formData.gst_number}
          onChange={handleInputChange}
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          placeholder="Enter GST number (optional)"
        />
      </div>

      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Business Address
        </label>
        <textarea
          name="business_address"
          value={formData.business_address}
          onChange={handleInputChange}
          rows={3}
          className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent resize-none"
          placeholder="Enter your business address"
        />
      </div>

      <div>
        <label className="block text-gray-700 text-sm font-medium mb-2">
          Handle/Website
        </label>
        <input
          type="url"
          name="handle_address"
          value={formData.handle_address}
          onChange={handleInputChange}
          className="w-full px-4 py-3 bg-white border border-gray-300 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          placeholder="Enter your website or social handle"
        />
      </div>
    </div>
  );

  return (
    <div className="fixed inset-0 bg-black bg-opacity-60 flex items-center justify-center z-[10001] p-4">
      <div className="bg-white rounded-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <h2 className="text-xl font-bold text-gray-900">
            Edit {type === 'user' ? 'Profile' : 'Provider Profile'}
          </h2>
          <button
            onClick={onClose}
            className="w-8 h-8 bg-gray-100 hover:bg-gray-200 rounded-full flex items-center justify-center transition-colors"
          >
            <X className="w-5 h-5 text-gray-600" />
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-6">
          {type === 'user' ? renderUserForm() : renderProviderForm()}

          {/* Buttons */}
          <div className="flex gap-3 mt-6">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-3 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors font-medium"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-4 py-3 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors font-medium disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
            >
              {loading ? (
                <>
                  <Loader className="w-4 h-4 animate-spin" />
                  Saving...
                </>
              ) : (
                <>
                  <Save className="w-4 h-4" />
                  Save Changes
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditProfileModal;
