// CategoryPills.jsx - Horizontal scrollable category filter pills
import React from 'react';

const CategoryPills = ({ categories, selected, onSelect }) => {
  const categoryNames = (categories || [])
    .map(c => c.category_name)
    .filter(name => name !== 'All'); // Remove 'All' if it exists in DB
  const allCategories = ['All', ...categoryNames];

  return (
    <div className="px-4 py-3 bg-black star-particles">
      <div className="flex gap-2 overflow-x-auto pb-1 relative z-10" style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}>
        {allCategories.map((category) => (
          <button
            key={category}
            onClick={() => onSelect(category)}
            className={`px-4 py-2 rounded-full whitespace-nowrap font-inter text-sm font-medium transition-all flex-shrink-0 ${
              selected === category
                ? 'bg-purple-600 text-white shadow-md'
                : 'bg-gray-800 text-white hover:bg-gray-700 border border-gray-700'
            }`}
            aria-label={`Filter by ${category}`}
          >
            {category}
          </button>
        ))}
      </div>
    </div>
  );
};

export default CategoryPills;

