import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { Upload, Video, Image as ImageIcon, Link as LinkIcon, DollarSign, FileText, Tag, Crown, Loader, ArrowLeft } from 'lucide-react';
import { subscriptionAPI, providerAPI, listingAPI, categoriesAPI } from '../utils/api';
import SubscriptionModal from '../components/SubscriptionModal';
import { toast } from 'react-hot-toast';
import Header from '../components/Header';

const UploadListing = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const listingType = searchParams.get('type') || 'PRODUCT';

  const [subscriptionStatus, setSubscriptionStatus] = useState(null);
  const [provider, setProvider] = useState(null);
  const [categories, setCategories] = useState([]);
  const [showSubscriptionModal, setShowSubscriptionModal] = useState(false);
  const [loading, setLoading] = useState(true);
  const [uploading, setUploading] = useState(false);

  const [formData, setFormData] = useState({
    type: listingType,
    product_name: '',
    price: '',
    description: '',
    thumbnail_url: '',
    video_url: '',
    link: '',
    category_id: ''
  });

  const [videoPreview, setVideoPreview] = useState(null);
  const [thumbnailPreview, setThumbnailPreview] = useState(null);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      setLoading(true);
      const [subStatus, categoriesData] = await Promise.all([
        subscriptionAPI.getStatus(),
        categoriesAPI.getAll()
      ]);

      setSubscriptionStatus(subStatus);
      setCategories(categoriesData.categories || []);

      try {
        const providerData = await providerAPI.getMe();
        setProvider(providerData.provider);
      } catch (error) {
        console.log('No provider profile found');
      }
    } catch (error) {
      console.error('Error loading data:', error);
      toast.error('Failed to load data');
    } finally {
      setLoading(false);
    }
  };

  const handleFileUpload = async (file, type) => {
    const STORAGE_SERVICE = process.env.REACT_APP_STORAGE_SERVICE || 'bunny';
    
    console.log('Environment variables check:', {
      STORAGE_SERVICE: process.env.REACT_APP_STORAGE_SERVICE,
      STORAGE_SERVICE_RESOLVED: STORAGE_SERVICE,
      CLOUDINARY_CLOUD_NAME: process.env.REACT_APP_CLOUDINARY_CLOUD_NAME,
      BUNNY_STORAGE_ZONE: process.env.REACT_APP_BUNNY_STORAGE_ZONE_NAME
    });
    
    console.log('Using storage service:', STORAGE_SERVICE);
    
    if (STORAGE_SERVICE === 'cloudinary') {
      console.log('Switching to Cloudinary upload...');
      return await uploadToCloudinary(file, type);
    } else if (STORAGE_SERVICE === 'bunny') {
      console.log('Switching to Bunny.net upload...');
      return await uploadToBunny(file, type);
    } else {
      throw new Error(`Unknown storage service: ${STORAGE_SERVICE}. Use 'cloudinary' or 'bunny'`);
    }
  };

  const uploadToCloudinary = async (file, type) => {
    const CLOUDINARY_CLOUD_NAME = process.env.REACT_APP_CLOUDINARY_CLOUD_NAME;
    const CLOUDINARY_UPLOAD_PRESET = process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET || 'getnayi_uploads';
    
    if (!CLOUDINARY_CLOUD_NAME) {
      throw new Error('Cloudinary configuration missing. Please set REACT_APP_CLOUDINARY_CLOUD_NAME in your .env file');
    }
    
    const formData = new FormData();
    formData.append('file', file);
    formData.append('upload_preset', CLOUDINARY_UPLOAD_PRESET);

    try {
      const response = await fetch(
        `https://api.cloudinary.com/v1_1/${CLOUDINARY_CLOUD_NAME}/${type}/upload`,
        {
          method: 'POST',
          body: formData
        }
      );

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error?.message || 'Upload failed');
      }

      const data = await response.json();
      console.log('Cloudinary upload successful:', data.secure_url);
      return data.secure_url;
    } catch (error) {
      console.error('Cloudinary upload error:', error);
      throw error;
    }
  };

  const uploadToBunny = async (file, type) => {
    const STORAGE_ZONE = process.env.REACT_APP_BUNNY_STORAGE_ZONE_NAME;
    const STORAGE_PASSWORD = process.env.REACT_APP_BUNNY_STORAGE_PASSWORD;
    const CDN_URL = process.env.REACT_APP_BUNNY_CDN_URL;
    
    console.log('Bunny.net Config:', {
      STORAGE_ZONE,
      STORAGE_PASSWORD: STORAGE_PASSWORD ? '***' : 'MISSING',
      CDN_URL
    });
    
    // Use FTP username as access key (this is usually the correct one)
    const FTP_USERNAME = process.env.REACT_APP_BUNNY_FTP_USERNAME;
    const accessKey = FTP_USERNAME || STORAGE_PASSWORD;
    console.log('Using access key:', accessKey ? '***' : 'MISSING');
    console.log('FTP Username available:', FTP_USERNAME ? 'YES' : 'NO');
    console.log('Storage Password available:', STORAGE_PASSWORD ? 'YES' : 'NO');
    console.log('Access key source:', FTP_USERNAME ? 'FTP_USERNAME' : 'STORAGE_PASSWORD');
    
    if (!STORAGE_ZONE || !STORAGE_PASSWORD || !CDN_URL) {
      throw new Error('Bunny.net configuration missing. Please set REACT_APP_BUNNY_STORAGE_ZONE_NAME, REACT_APP_BUNNY_STORAGE_PASSWORD, and REACT_APP_BUNNY_CDN_URL in your .env file');
    }
    
    if (!accessKey) {
      throw new Error('No access key available. Please set either REACT_APP_BUNNY_STORAGE_PASSWORD or REACT_APP_BUNNY_FTP_USERNAME');
    }
    
    // Generate unique filename
    const timestamp = Date.now();
    const randomId = Math.random().toString(36).substring(2, 15);
    const fileExtension = file.name.split('.').pop();
    const fileName = `${type}/${timestamp}_${randomId}.${fileExtension}`;
    
    const uploadUrl = `https://storage.bunnycdn.com/${STORAGE_ZONE}/${fileName}`;
    console.log('Upload URL:', uploadUrl);
    console.log('File info:', {
      name: file.name,
      size: file.size,
      type: file.type
    });
    
    try {
      const response = await fetch(uploadUrl, {
        method: 'PUT',
        headers: {
          'AccessKey': accessKey,
          'Content-Type': file.type,
        },
        body: file
      });

      console.log('Response status:', response.status);
      console.log('Response headers:', Object.fromEntries(response.headers.entries()));

      if (!response.ok) {
        const errorText = await response.text();
        console.error('Upload failed:', {
          status: response.status,
          statusText: response.statusText,
          errorText
        });
        throw new Error(`Upload failed: ${response.status} ${response.statusText} - ${errorText}`);
      }

      const cdnUrl = `${CDN_URL}/${fileName}`;
      console.log('Bunny.net upload successful, CDN URL:', cdnUrl);
      return cdnUrl;
    } catch (error) {
      console.error('Bunny.net upload error:', error);
      throw error;
    }
  };

  const handleVideoChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const STORAGE_SERVICE = process.env.REACT_APP_STORAGE_SERVICE || 'bunny';
    const maxVideoSize = STORAGE_SERVICE === 'cloudinary' ? 100 : 500; // MB
    
    if (file.size > maxVideoSize * 1024 * 1024) {
      toast.error(`Video file too large. Max ${maxVideoSize}MB for ${STORAGE_SERVICE}`);
      return;
    }

    setUploading(true);
    toast.loading('Uploading video...', { id: 'video-upload' });

    try {
      const url = await handleFileUpload(file, 'video');
      setFormData({ ...formData, video_url: url });
      setVideoPreview(URL.createObjectURL(file));
      toast.success('Video uploaded successfully', { id: 'video-upload' });
    } catch (error) {
      console.error('Video upload error:', error);
      toast.error(`Failed to upload video: ${error.message}`, { id: 'video-upload' });
    } finally {
      setUploading(false);
    }
  };

  const handleThumbnailChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const STORAGE_SERVICE = process.env.REACT_APP_STORAGE_SERVICE || 'bunny';
    const maxImageSize = STORAGE_SERVICE === 'cloudinary' ? 5 : 20; // MB
    
    if (file.size > maxImageSize * 1024 * 1024) {
      toast.error(`Image file too large. Max ${maxImageSize}MB for ${STORAGE_SERVICE}`);
      return;
    }

    setUploading(true);
    toast.loading('Uploading thumbnail...', { id: 'thumb-upload' });

    try {
      const url = await handleFileUpload(file, 'image');
      setFormData({ ...formData, thumbnail_url: url });
      setThumbnailPreview(URL.createObjectURL(file));
      toast.success('Thumbnail uploaded successfully', { id: 'thumb-upload' });
    } catch (error) {
      console.error('Thumbnail upload error:', error);
      toast.error(`Failed to upload thumbnail: ${error.message}`, { id: 'thumb-upload' });
    } finally {
      setUploading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!provider) {
      toast.error('Please create a provider profile first');
      navigate('/provider/register');
      return;
    }

    if (subscriptionStatus.uploads_remaining === 0) {
      toast.error('Upload limit reached. Please subscribe!');
      setShowSubscriptionModal(true);
      return;
    }

    setUploading(true);
    try {
      const response = await listingAPI.create(formData);
      toast.success('Listing created successfully!');
      navigate('/listing/my');
    } catch (error) {
      console.error('Error creating listing:', error);
      toast.error(error.response?.data?.error || 'Failed to create listing');
    } finally {
      setUploading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black star-particles">
        <Loader className="w-8 h-8 animate-spin text-purple-500" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      
      <div className="pt-16 pb-24 px-4 max-w-4xl mx-auto relative z-10">
        {/* Back Button */}
        <button
          onClick={() => navigate('/listing')}
          className="flex items-center gap-2 text-gray-400 hover:text-white mb-6 transition-colors font-inter"
        >
          <ArrowLeft className="w-5 h-5" />
          Back
        </button>

        <h1 className="text-3xl font-bold text-white mb-6 font-inter">
          Upload {listingType === 'PRODUCT' ? 'Product' : 'Service'}
        </h1>

        {/* Subscription Status Card */}
        <div className="bg-gradient-to-r from-purple-900 to-purple-800 text-white rounded-xl p-6 mb-6 shadow-lg border border-purple-700">
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-lg font-semibold mb-2 font-inter">
                {subscriptionStatus?.is_subscribed ? 'Premium Member' : 'Free Plan'}
              </h3>
              <p className="text-sm opacity-90 font-inter">
                {subscriptionStatus?.is_subscribed ? (
                  <>Unlimited uploads  Expires: {new Date(subscriptionStatus.subscription_expiration).toLocaleDateString()}</>
                ) : (
                  <>Uploads remaining: {subscriptionStatus?.uploads_remaining}/5</>
                )}
              </p>
            </div>
            {!subscriptionStatus?.is_subscribed && (
              <button
                onClick={() => setShowSubscriptionModal(true)}
                className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-2 rounded-full font-semibold transition-all flex items-center gap-2 font-inter"
              >
                <Crown className="w-4 h-4" />
                Upgrade
              </button>
            )}
          </div>
        </div>

        {/* Upload Form */}
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Video Upload */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-6">
            <h3 className="text-lg font-semibold mb-4 flex items-center gap-2 font-inter text-white">
              <Video className="w-5 h-5 text-purple-500" />
              Video
            </h3>
            <div className="border-2 border-dashed border-gray-600 rounded-xl p-8 text-center hover:border-purple-400 transition-colors">
              {videoPreview ? (
                <div className="space-y-4">
                  <video src={videoPreview} controls className="mx-auto max-h-64 rounded-lg" />
                  <button
                    type="button"
                    onClick={() => {
                      setVideoPreview(null);
                      setFormData({ ...formData, video_url: '' });
                    }}
                    className="text-red-400 hover:text-red-300 text-sm font-inter"
                  >
                    Remove Video
                  </button>
                </div>
              ) : (
                <div>
                  <Upload className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                  <label className="cursor-pointer">
                    <span className="text-purple-500 hover:text-purple-400 font-medium font-inter">
                      Click to upload video
                    </span>
                    <input
                      type="file"
                      accept="video/*"
                      onChange={handleVideoChange}
                      className="hidden"
                      disabled={uploading}
                    />
                  </label>
                  <p className="text-sm text-gray-400 mt-2 font-inter">
                    MP4, MOV up to {process.env.REACT_APP_STORAGE_SERVICE === 'cloudinary' ? '100MB' : '500MB'}
                  </p>
                </div>
              )}
            </div>
          </div>

          {/* Thumbnail Upload */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-6">
            <h3 className="text-lg font-semibold mb-4 flex items-center gap-2 font-inter text-white">
              <ImageIcon className="w-5 h-5 text-purple-500" />
              Thumbnail
            </h3>
            <div className="border-2 border-dashed border-gray-600 rounded-xl p-8 text-center hover:border-purple-400 transition-colors">
              {thumbnailPreview ? (
                <div className="space-y-4">
                  <img src={thumbnailPreview} alt="Thumbnail" className="mx-auto max-h-64 rounded-lg" />
                  <button
                    type="button"
                    onClick={() => {
                      setThumbnailPreview(null);
                      setFormData({ ...formData, thumbnail_url: '' });
                    }}
                    className="text-red-400 hover:text-red-300 text-sm font-inter"
                  >
                    Remove Image
                  </button>
                </div>
              ) : (
                <div>
                  <ImageIcon className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                  <label className="cursor-pointer">
                    <span className="text-purple-500 hover:text-purple-400 font-medium font-inter">
                      Click to upload thumbnail
                    </span>
                    <input
                      type="file"
                      accept="image/*"
                      onChange={handleThumbnailChange}
                      className="hidden"
                      disabled={uploading}
                    />
                  </label>
                  <p className="text-sm text-gray-400 mt-2 font-inter">
                    JPG, PNG up to {process.env.REACT_APP_STORAGE_SERVICE === 'cloudinary' ? '5MB' : '20MB'}
                  </p>
                </div>
              )}
            </div>
          </div>

          {/* Product Details */}
          <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-sm p-6 space-y-4">
            <h3 className="text-lg font-semibold mb-4 font-inter text-white">Details</h3>
            
            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                {listingType === 'PRODUCT' ? 'Product' : 'Service'} Name *
              </label>
              <input
                type="text"
                required
                value={formData.product_name}
                onChange={(e) => setFormData({ ...formData, product_name: e.target.value })}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="Enter name"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                Price () *
              </label>
              <div className="relative">
                <DollarSign className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                <input
                  type="number"
                  required
                  min="0"
                  step="0.01"
                  value={formData.price}
                  onChange={(e) => setFormData({ ...formData, price: e.target.value })}
                  className="w-full pl-10 pr-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                  placeholder="0.00"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                Description *
              </label>
              <textarea
                required
                rows={4}
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                placeholder="Describe your product or service"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                Category
              </label>
              <select
                value={formData.category_id}
                onChange={(e) => setFormData({ ...formData, category_id: e.target.value })}
                className="w-full px-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white"
              >
                <option value="">Select a category</option>
                {categories.map(cat => (
                  <option key={cat.category_id} value={cat.category_id}>
                    {cat.category_name}
                  </option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-300 mb-2 font-inter">
                Link (Website/Store)
              </label>
              <div className="relative">
                <LinkIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                <input
                  type="url"
                  value={formData.link}
                  onChange={(e) => setFormData({ ...formData, link: e.target.value })}
                  className="w-full pl-10 pr-4 py-3 border border-gray-600 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent font-inter bg-gray-700 text-white placeholder-gray-400"
                  placeholder="https://your-website.com"
                />
              </div>
            </div>
          </div>

          {/* Submit Button */}
          <div className="pt-4">
            <button
              type="submit"
              disabled={uploading}
              className="w-full bg-gradient-to-r from-purple-600 to-purple-700 text-white py-4 rounded-xl font-semibold hover:from-purple-700 hover:to-purple-800 transition-all disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2 shadow-lg font-inter"
            >
              {uploading ? (
                <>
                  <Loader className="w-5 h-5 animate-spin" />
                  Creating Listing...
                </>
              ) : (
                <>
                  <Upload className="w-5 h-5" />
                  Publish Listing
                </>
              )}
            </button>
          </div>
        </form>
      </div>


      {/* Subscription Modal */}
      {showSubscriptionModal && (
        <SubscriptionModal
          onClose={() => setShowSubscriptionModal(false)}
          onSuccess={() => {
            setShowSubscriptionModal(false);
            loadData();
          }}
        />
      )}
    </div>
  );
};

export default UploadListing;
