import axios from 'axios';

const API_BASE_URL = 'https://backend.getnayi.in';

const api = axios.create({
  baseURL: API_BASE_URL,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json',
  },
});

export const authAPI = {
  requestOTP: async (phone) => {
    const response = await api.post('/api/auth/request-otp', { phone });
    return response.data;
  },

  verifyOTP: async (phone, code) => {
    const response = await api.post('/api/auth/verify-otp', { phone, code });
    return response.data;
  },

  signup: async (onboardingToken, username, full_name, preferences) => {
    const response = await api.post('/api/auth/signup', {
      onboardingToken,
      username,
      full_name,
      preferences,
    });
    return response.data;
  },

  getCurrentUser: async () => {
    const response = await api.get('/api/me');
    return response.data;
  },

  logout: async () => {
    const response = await api.post('/api/auth/logout');
    return response.data;
  },
};

export const categoriesAPI = {
  getAll: async () => {
    const response = await api.get('/api/categories');
    return response.data;
  },

};

