export const watchAPI = {
  getFeed: async (limit = 20, offset = 0) => {
    const response = await api.get('/api/watch/feed', { 
      params: { limit, offset } 
    });
    return response.data;
  },

  getFeedWithAdminPriority: async (limit = 20, offset = 0) => {
    const response = await api.get('/api/watch/feed/admin-priority', { 
      params: { limit, offset } 
    });
    return response.data;
  },
  
  getVideoDetails: async (listingId) => {
    const response = await api.get(`/api/watch/${listingId}`);
    return response.data;
  },
  
  engage: async (listingId, action) => {
    const response = await api.post(`/api/watch/${listingId}/engage`, {
      listing_id: listingId,
      action: action
    });
    return response.data;
  },
  
  like: async (listingId) => {
    const response = await api.post(`/api/watch/${listingId}/like`);
    return response.data;
  },
  
  unlike: async (listingId) => {
    const response = await api.delete(`/api/watch/${listingId}/like`);
    return response.data;
  },
  
  getLikeStatus: async (listingId) => {
    const response = await api.get(`/api/watch/${listingId}/like/status`);
    return response.data;
  },
  
  love: async (listingId) => {
    return await watchAPI.engage(listingId, 'love');
  },
  
  view: async (listingId) => {
    return await watchAPI.engage(listingId, 'view');
  },
  
  click: async (listingId) => {
    return await watchAPI.engage(listingId, 'click');
  }
};

