import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const VideoManagement = () => {
  const [activeTab, setActiveTab] = useState('pending');
  const [videos, setVideos] = useState([]);
  const [pendingVideos, setPendingVideos] = useState([]);
  const [approvedVideos, setApprovedVideos] = useState([]);
  const [flaggedVideos, setFlaggedVideos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedVideo, setSelectedVideo] = useState(null);
  const [showVideoModal, setShowVideoModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState('');
  const [bulkSelected, setBulkSelected] = useState([]);
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [adminVideos, setAdminVideos] = useState([]);
  const [uploadForm, setUploadForm] = useState({
    product_name: '',
    description: '',
    price: '',
    video_url: '',
    video_category: 'USER_CONTENT'
  });

  useEffect(() => {
    fetchVideos();
    fetchAdminVideos();
  }, []);

  const fetchVideos = async () => {
    try {
      setLoading(true);
      const response = await adminAPI.getVideos();
      
      if (response.ok) {
        const data = response.data;
        setVideos(data.videos || []);
        setPendingVideos(data.pending || []);
        setApprovedVideos(data.approved || []);
        setFlaggedVideos(data.flagged || []);
      } else {
        setError(response.message || 'Failed to load videos');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const fetchAdminVideos = async () => {
    try {
      const response = await adminAPI.getAdminVideos();
      if (response.ok) {
        setAdminVideos(response.data.admin_videos || []);
      }
    } catch (err) {
      console.error('Failed to fetch admin videos:', err);
    }
  };

  const handleVideoAction = async (videoId, action, reason = '', category = '') => {
    try {
      setActionLoading(videoId);
      const response = await adminAPI.moderateVideo(videoId, action, reason, category);
      
      if (response.ok) {
        await fetchVideos();
        if (selectedVideo && selectedVideo.id === videoId) {
          setShowVideoModal(false);
          setSelectedVideo(null);
        }
      } else {
        setError(response.message || 'Failed to update video status');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const handleBulkAction = async (action, reason = '', category = '') => {
    if (bulkSelected.length === 0) return;
    
    try {
      setActionLoading('bulk');
      const response = await adminAPI.bulkModerateVideos(bulkSelected, action, reason, category);
      
      if (response.ok) {
        await fetchVideos();
        setBulkSelected([]);
      } else {
        setError(response.message || 'Failed to update videos');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const openVideoModal = async (video) => {
    try {
      const response = await adminAPI.getVideoDetails(video.id);
      if (response.ok) {
        setSelectedVideo(response.data);
        setShowVideoModal(true);
      } else {
        setError(response.message || 'Failed to load video details');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const handleAdminVideoUpload = async (e) => {
    e.preventDefault();
    try {
      setActionLoading('upload');
      const response = await adminAPI.uploadAdminVideo(uploadForm);
      
      if (response.ok) {
        await fetchVideos();
        await fetchAdminVideos();
        setShowUploadModal(false);
        setUploadForm({
          product_name: '',
          description: '',
          price: '',
          video_url: '',
          video_category: 'USER_CONTENT'
        });
      } else {
        setError(response.message || 'Failed to upload video');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'APPROVED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'FLAGGED': return 'bg-red-100 text-red-800';
      case 'REJECTED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getCategoryBadgeColor = (category) => {
    switch (category) {
      case 'PROMOTION': return 'bg-purple-100 text-purple-800';
      case 'BRAND_ADS': return 'bg-blue-100 text-blue-800';
      case 'PLATFORM_PROMO': return 'bg-indigo-100 text-indigo-800';
      case 'USER_CONTENT': return 'bg-gray-100 text-gray-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredVideos = (list) => {
    let filtered = list;
    
    if (searchTerm) {
      filtered = filtered.filter(video => 
        video.product_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        video.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        video.uploader_name?.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    
    if (categoryFilter) {
      filtered = filtered.filter(video => video.category === categoryFilter);
    }
    
    return filtered;
  };

  const VideoCard = ({ video, showActions = true, showCheckbox = false }) => (
    <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-100 hover:shadow-xl transition-all duration-300">
      <div className="flex items-start space-x-4">
        {showCheckbox && (
          <input
            type="checkbox"
            checked={bulkSelected.includes(video.id)}
            onChange={(e) => {
              if (e.target.checked) {
                setBulkSelected([...bulkSelected, video.id]);
              } else {
                setBulkSelected(bulkSelected.filter(id => id !== video.id));
              }
            }}
            className="mt-2 h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
        )}
        
        {/* Video Thumbnail */}
        <div className="w-24 h-24 bg-gray-200 rounded-xl flex items-center justify-center overflow-hidden">
          {video.thumbnail_url ? (
            <img src={video.thumbnail_url} alt={video.product_name} className="w-full h-full object-cover" />
          ) : (
            <svg className="w-8 h-8 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
            </svg>
          )}
        </div>

        {/* Video Info */}
        <div className="flex-1">
          <div className="flex items-start justify-between">
            <div>
              <h3 className="text-lg font-bold text-gray-900 mb-1">{video.product_name}</h3>
              <p className="text-gray-600 mb-2 line-clamp-2">{video.description}</p>
              <div className="flex items-center space-x-3 mb-3">
                <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(video.video_status)}`}>
                  {video.video_status || 'PENDING'}
                </span>
                <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getCategoryBadgeColor(video.category)}`}>
                  {video.category || 'USER_CONTENT'}
                </span>
                <span className="text-sm text-gray-500">${video.price}</span>
              </div>
              
              {/* Uploader Info */}
              <div className="flex items-center space-x-2 text-sm text-gray-500 mb-3">
                <span>by {video.uploader_name}</span>
                <span></span>
                <span>{new Date(video.created_at).toLocaleDateString()}</span>
              </div>

              {/* Stats */}
              <div className="grid grid-cols-4 gap-4 text-center">
                <div>
                  <p className="text-lg font-bold text-blue-600">{video.view_count || 0}</p>
                  <p className="text-xs text-gray-500">Views</p>
                </div>
                <div>
                  <p className="text-lg font-bold text-red-600">{video.like_count || 0}</p>
                  <p className="text-xs text-gray-500">Likes</p>
                </div>
                <div>
                  <p className="text-lg font-bold text-purple-600">{video.love_count || 0}</p>
                  <p className="text-xs text-gray-500">Loves</p>
                </div>
                <div>
                  <p className="text-lg font-bold text-green-600">{video.click_count || 0}</p>
                  <p className="text-xs text-gray-500">Clicks</p>
                </div>
              </div>
            </div>

            {/* Actions */}
            <div className="flex flex-col space-y-2">
              <button
                onClick={() => openVideoModal(video)}
                className="bg-blue-600 text-white px-4 py-2 rounded-xl hover:bg-blue-700 transition-colors text-sm font-medium"
              >
                Review
              </button>
              {showActions && video.video_status === 'PENDING' && (
                <div className="flex space-x-2">
                  <button
                    onClick={() => handleVideoAction(video.id, 'approve')}
                    disabled={actionLoading === video.id}
                    className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-xs font-medium disabled:opacity-50"
                  >
                    {actionLoading === video.id ? '...' : 'Approve'}
                  </button>
                  <button
                    onClick={() => {
                      const reason = prompt('Flag reason:');
                      if (reason) {
                        handleVideoAction(video.id, 'flag', reason);
                      }
                    }}
                    disabled={actionLoading === video.id}
                    className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-xs font-medium disabled:opacity-50"
                  >
                    {actionLoading === video.id ? '...' : 'Flag'}
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Videos</h2>
          <p className="text-lg text-gray-600">Fetching video data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">Video Management</h1>
              <p className="text-gray-600 text-lg">Moderate and manage all uploaded videos</p>
            </div>
            <div className="flex items-center space-x-4">
              <button 
                onClick={() => setShowUploadModal(true)}
                className="bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-all duration-200 flex items-center space-x-3 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                </svg>
                <span className="font-semibold">Upload Video</span>
              </button>
              <button 
                onClick={fetchVideos}
                className="bg-blue-600 text-white px-6 py-3 rounded-xl hover:bg-blue-700 transition-all duration-200 flex items-center space-x-3 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                <span className="font-semibold">Refresh</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="px-6 py-8">
        {/* Filters */}
        <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-100 mb-8">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="relative">
              <input
                type="text"
                placeholder="Search videos, uploaders..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
              />
              <svg className="absolute left-3 top-3.5 w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
            </div>
            <select
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
              className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="">All Categories</option>
              <option value="PROMOTION">Promotion</option>
              <option value="BRAND_ADS">Brand Ads</option>
              <option value="PLATFORM_PROMO">Platform Promo</option>
              <option value="USER_CONTENT">User Content</option>
            </select>
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-600">Bulk Actions:</span>
              {bulkSelected.length > 0 && (
                <div className="flex space-x-2">
                  <button
                    onClick={() => handleBulkAction('approve')}
                    disabled={actionLoading === 'bulk'}
                    className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-xs font-medium disabled:opacity-50"
                  >
                    Approve ({bulkSelected.length})
                  </button>
                  <button
                    onClick={() => {
                      const reason = prompt('Flag reason:');
                      if (reason) {
                        handleBulkAction('flag', reason);
                      }
                    }}
                    disabled={actionLoading === 'bulk'}
                    className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-xs font-medium disabled:opacity-50"
                  >
                    Flag ({bulkSelected.length})
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
            <div className="flex items-center">
              <svg className="w-5 h-5 text-red-600 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800 font-medium">{error}</p>
            </div>
          </div>
        )}

        {/* Tabs */}
        <div className="bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden mb-8">
          <div className="border-b border-gray-200">
            <nav className="flex">
              <button
                onClick={() => setActiveTab('pending')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'pending'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Pending Review ({filteredVideos(pendingVideos).length})
              </button>
              <button
                onClick={() => setActiveTab('approved')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'approved'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Approved ({filteredVideos(approvedVideos).length})
              </button>
              <button
                onClick={() => setActiveTab('flagged')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'flagged'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Flagged ({filteredVideos(flaggedVideos).length})
              </button>
              <button
                onClick={() => setActiveTab('admin')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'admin'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Admin Videos ({adminVideos.length})
              </button>
              <button
                onClick={() => setActiveTab('all')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'all'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                All Videos ({filteredVideos(videos).length})
              </button>
            </nav>
          </div>

          <div className="p-6">
            {activeTab === 'pending' && (
              <div className="space-y-6">
                {filteredVideos(pendingVideos).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No pending videos</p>
                    <p className="text-sm text-gray-500 mt-2">All videos have been reviewed</p>
                  </div>
                ) : (
                  filteredVideos(pendingVideos).map((video) => (
                    <VideoCard key={video.id} video={video} showActions={true} showCheckbox={true} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'approved' && (
              <div className="space-y-6">
                {filteredVideos(approvedVideos).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No approved videos</p>
                    <p className="text-sm text-gray-500 mt-2">Approved videos will appear here</p>
                  </div>
                ) : (
                  filteredVideos(approvedVideos).map((video) => (
                    <VideoCard key={video.id} video={video} showActions={false} showCheckbox={false} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'flagged' && (
              <div className="space-y-6">
                {filteredVideos(flaggedVideos).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No flagged videos</p>
                    <p className="text-sm text-gray-500 mt-2">Flagged videos will appear here</p>
                  </div>
                ) : (
                  filteredVideos(flaggedVideos).map((video) => (
                    <VideoCard key={video.id} video={video} showActions={false} showCheckbox={false} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'admin' && (
              <div className="space-y-6">
                {adminVideos.length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No admin videos uploaded</p>
                    <p className="text-sm text-gray-500 mt-2">Click "Upload Video" to add your first admin video</p>
                  </div>
                ) : (
                  adminVideos.map((video) => (
                    <VideoCard key={video.id} video={{...video, is_admin_video: true}} showActions={false} showCheckbox={false} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'all' && (
              <div className="space-y-6">
                {filteredVideos(videos).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No videos found</p>
                    <p className="text-sm text-gray-500 mt-2">Try adjusting your search criteria</p>
                  </div>
                ) : (
                  filteredVideos(videos).map((video) => (
                    <VideoCard key={video.id} video={video} showActions={false} showCheckbox={false} />
                  ))
                )}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Video Details Modal */}
      {showVideoModal && selectedVideo && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowVideoModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-6xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-2xl font-bold text-gray-900">Video Review</h3>
                  <button
                    onClick={() => setShowVideoModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                  {/* Video Player */}
                  <div>
                    <div className="aspect-video bg-gray-200 rounded-xl mb-4">
                      {selectedVideo.video_url ? (
                        <video
                          src={selectedVideo.video_url}
                          controls
                          className="w-full h-full rounded-xl"
                        />
                      ) : (
                        <div className="w-full h-full flex items-center justify-center">
                          <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                          </svg>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Video Details */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Video Information</h4>
                      <div className="space-y-3">
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Title</label>
                          <p className="text-gray-900">{selectedVideo.product_name}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Description</label>
                          <p className="text-gray-900">{selectedVideo.description}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Price</label>
                          <p className="text-gray-900">${selectedVideo.price}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Uploader</label>
                          <p className="text-gray-900">{selectedVideo.uploader_name}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Status</label>
                          <span className={`inline-block px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(selectedVideo.video_status)}`}>
                            {selectedVideo.video_status || 'PENDING'}
                          </span>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Category</label>
                          <span className={`inline-block px-3 py-1.5 rounded-full text-xs font-semibold ${getCategoryBadgeColor(selectedVideo.category)}`}>
                            {selectedVideo.category || 'USER_CONTENT'}
                          </span>
                        </div>
                      </div>
                    </div>

                    {/* Statistics */}
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Statistics</h4>
                      <div className="grid grid-cols-2 gap-4">
                        <div className="bg-blue-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-blue-600">{selectedVideo.view_count || 0}</p>
                          <p className="text-blue-700 text-sm font-medium">Views</p>
                        </div>
                        <div className="bg-red-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-red-600">{selectedVideo.like_count || 0}</p>
                          <p className="text-red-700 text-sm font-medium">Likes</p>
                        </div>
                        <div className="bg-purple-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-purple-600">{selectedVideo.love_count || 0}</p>
                          <p className="text-purple-700 text-sm font-medium">Loves</p>
                        </div>
                        <div className="bg-green-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-green-600">{selectedVideo.click_count || 0}</p>
                          <p className="text-green-700 text-sm font-medium">Clicks</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Action Buttons */}
                <div className="flex items-center justify-end space-x-4 mt-8 pt-6 border-t border-gray-200">
                  {selectedVideo.video_status === 'PENDING' && (
                    <>
                      <button
                        onClick={() => {
                          const category = prompt('Category (PROMOTION, BRAND_ADS, PLATFORM_PROMO, USER_CONTENT):', selectedVideo.category || 'USER_CONTENT');
                          if (category) {
                            handleVideoAction(selectedVideo.id, 'approve', '', category);
                          }
                        }}
                        disabled={actionLoading === selectedVideo.id}
                        className="bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedVideo.id ? 'Processing...' : 'Approve Video'}
                      </button>
                      <button
                        onClick={() => {
                          const reason = prompt('Flag reason:');
                          if (reason) {
                            handleVideoAction(selectedVideo.id, 'flag', reason);
                          }
                        }}
                        disabled={actionLoading === selectedVideo.id}
                        className="bg-red-600 text-white px-6 py-3 rounded-xl hover:bg-red-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedVideo.id ? 'Processing...' : 'Flag Video'}
                      </button>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Admin Video Upload Modal */}
      {showUploadModal && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowUploadModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-2xl font-bold text-gray-900">Upload Admin Video</h3>
                  <button
                    onClick={() => setShowUploadModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <form onSubmit={handleAdminVideoUpload} className="space-y-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Video Title</label>
                    <input
                      type="text"
                      required
                      value={uploadForm.product_name}
                      onChange={(e) => setUploadForm({...uploadForm, product_name: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
                      placeholder="Enter video title"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Description</label>
                    <textarea
                      required
                      rows={3}
                      value={uploadForm.description}
                      onChange={(e) => setUploadForm({...uploadForm, description: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      placeholder="Enter video description"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Price</label>
                    <input
                      type="number"
                      required
                      min="0"
                      step="0.01"
                      value={uploadForm.price}
                      onChange={(e) => setUploadForm({...uploadForm, price: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
                      placeholder="Enter price"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Video URL</label>
                    <input
                      type="url"
                      required
                      value={uploadForm.video_url}
                      onChange={(e) => setUploadForm({...uploadForm, video_url: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
                      placeholder="Enter video URL"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Category</label>
                    <select
                      required
                      value={uploadForm.video_category}
                      onChange={(e) => setUploadForm({...uploadForm, video_category: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="USER_CONTENT">User Content</option>
                      <option value="PROMOTION">Promotion</option>
                      <option value="BRAND_ADS">Brand Ads</option>
                      <option value="PLATFORM_PROMO">Platform Promo</option>
                    </select>
                  </div>

                  <div className="flex items-center justify-end space-x-4 pt-6 border-t border-gray-200">
                    <button
                      type="button"
                      onClick={() => setShowUploadModal(false)}
                      className="px-6 py-3 border border-gray-300 rounded-xl text-gray-700 hover:bg-gray-50 transition-colors font-semibold"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      disabled={actionLoading === 'upload'}
                      className="bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-colors font-semibold disabled:opacity-50"
                    >
                      {actionLoading === 'upload' ? 'Uploading...' : 'Upload Video'}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default VideoManagement;
