import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { authAPI, categoriesAPI } from '../utils/api';
import { useAuth } from '../context/AuthContext';

const Signup = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { login } = useAuth();
  
  const onboardingToken = location.state?.onboardingToken;

  const [step, setStep] = useState('info'); // 'info' or 'preferences'
  const [username, setUsername] = useState('');
  const [fullName, setFullName] = useState('');
  const [categories, setCategories] = useState([]);
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (!onboardingToken) {
      navigate('/login');
      return;
    }
    fetchCategories();
  }, [onboardingToken, navigate]);

  const fetchCategories = async () => {
    try {
      const data = await categoriesAPI.getAll();
      setCategories(data.categories || []);
    } catch (err) {
      setError('Failed to load categories');
    }
  };

  const handleInfoSubmit = (e) => {
    e.preventDefault();
    
    if (!username.trim() || !fullName.trim()) {
      setError('Username and full name are required');
      return;
    }

    if (username.length < 3) {
      setError('Username must be at least 3 characters');
      return;
    }

    setError('');
    setStep('preferences');
  };

  const toggleCategory = (slug) => {
    setSelectedCategories(prev => {
      if (prev.includes(slug)) {
        return prev.filter(s => s !== slug);
      } else {
        return [...prev, slug];
      }
    });
  };

  const handleSignupComplete = async () => {
    if (selectedCategories.length < 1) {
      setError('Please select at least 1 category');
      return;
    }

    setLoading(true);
    setError('');

    try {
      const data = await authAPI.signup(
        onboardingToken,
        username.trim(),
        fullName.trim(),
        selectedCategories
      );

      login(data.user);
      navigate('/');
    } catch (err) {
      setError(err.response?.data?.error || 'Signup failed');
    } finally {
      setLoading(false);
    }
  };

  if (step === 'info') {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center p-4 sm:p-6 star-particles">
        <div className="bg-black border border-gray-700 rounded-2xl shadow-xl p-6 sm:p-8 w-full max-w-md relative z-10">
          <div className="text-center mb-6 sm:mb-8">
            <h1 className="text-2xl sm:text-3xl font-bold text-white mb-2">Create Your Account</h1>
            <p className="text-sm sm:text-base text-gray-400">Tell us about yourself</p>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-900/50 border border-red-700 text-red-400 rounded-lg text-xs sm:text-sm">
              {error}
            </div>
          )}

          <form onSubmit={handleInfoSubmit}>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-300 mb-2">
                Username
              </label>
              <input
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value.toLowerCase().replace(/[^a-z0-9_]/g, ''))}
                placeholder="john_doe"
                className="w-full px-4 py-4 text-base border border-gray-600 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent bg-gray-800 text-white placeholder-gray-400"
                required
              />
            </div>

            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-300 mb-2">
                Full Name
              </label>
              <input
                type="text"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                placeholder="John Doe"
                className="w-full px-4 py-4 text-base border border-gray-600 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent bg-gray-800 text-white placeholder-gray-400"
                required
              />
            </div>

            <button
              type="submit"
              className="w-full bg-purple-600 text-white py-4 rounded-xl text-base font-medium active:bg-purple-700 transition-colors touch-manipulation"
            >
              Continue
            </button>
          </form>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black p-4 sm:p-6 star-particles">
      <div className="max-w-4xl mx-auto py-4 sm:py-8">
        <div className="bg-black border border-gray-700 rounded-2xl shadow-xl p-4 sm:p-8 relative z-10">
          <div className="text-center mb-6 sm:mb-8">
            <h1 className="text-xl sm:text-3xl font-bold text-white mb-2">Choose Your Interests</h1>
            <p className="text-sm sm:text-base text-gray-400">Select at least 1 category you love</p>
            <div className="mt-2 inline-block px-4 py-2 bg-purple-600 text-white rounded-full text-sm font-medium">
              {selectedCategories.length} selected
            </div>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-900/50 border border-red-700 text-red-400 rounded-lg text-xs sm:text-sm">
              {error}
            </div>
          )}

          <div className="grid grid-cols-2 sm:grid-cols-3 gap-3 sm:gap-4 mb-6 sm:mb-8">
            {categories.map((category) => (
              <button
                key={category.category_id}
                onClick={() => toggleCategory(category.slug)}
                className={`p-4 sm:p-6 rounded-xl border-2 transition-all touch-manipulation ${
                  selectedCategories.includes(category.slug)
                    ? 'border-purple-500 bg-purple-900/50 scale-95'
                    : 'border-gray-600 active:border-purple-400 bg-gray-800'
                }`}
              >
                {category.image_url && (
                  <img 
                    src={category.image_url} 
                    alt={category.category_name}
                    className="w-12 h-12 sm:w-16 sm:h-16 mx-auto mb-2 sm:mb-3 object-cover rounded-lg"
                  />
                )}
                <h3 className="font-semibold text-white text-center text-sm sm:text-base">
                  {category.category_name}
                </h3>
                {selectedCategories.includes(category.slug) && (
                  <div className="mt-2 text-center">
                    <span className="inline-block w-6 h-6 bg-purple-500 text-white rounded-full text-sm leading-6">
                      
                    </span>
                  </div>
                )}
              </button>
            ))}
          </div>

          <div className="space-y-3">
            <button
              onClick={handleSignupComplete}
              disabled={loading || selectedCategories.length < 1}
              className="w-full px-8 py-4 bg-purple-600 text-white rounded-xl text-base font-medium active:bg-purple-700 disabled:bg-gray-600 transition-colors touch-manipulation"
            >
              {loading ? 'Creating Account...' : 'Get Started'}
            </button>

            <button
              onClick={() => setStep('info')}
              className="w-full px-6 py-3 text-gray-400 active:text-gray-300 font-medium touch-manipulation"
              disabled={loading}
            >
               Back
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Signup;

