const Home = () => {
  const navigate = useNavigate();
  const [sheetOpen, setSheetOpen] = useState(false);
  const [selectedInfluencer, setSelectedInfluencer] = useState(null);
  const [selectedProducts, setSelectedProducts] = useState([]);
  const [sections, setSections] = useState([]);
  const [allListings, setAllListings] = useState([]);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [wishlistIds, setWishlistIds] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadDynamicData();
  }, []);

  useEffect(() => {
    // Re-organize sections when category filter changes
    if (allListings.length > 0) {
      organizeSections(allListings, selectedCategory);
    }
  }, [selectedCategory]);

  const transformListingToItem = (listing) => ({
    id: listing.listing_id,
    listing_id: listing.listing_id,
    title: listing.product_name || 'Product',
    thumbnail: listing.thumbnail_url || 'https://picsum.photos/200/267?random=' + listing.listing_id,
    influencerName: listing.provider_name || listing.handle_address || 'Provider',
    featuredProduct: { 
      name: listing.product_name || 'Product', 
      price: listing.price || 0
    },
    videoUrl: listing.video_url,
    description: listing.description,
    link: listing.link,
    type: listing.type,
    category_name: listing.category_name
  });

  const organizeSections = (listingsArray, categoryFilter) => {
    let filteredListings = listingsArray;
    
    console.log('Filtering by:', categoryFilter);
    console.log('Available listings:', listingsArray.map(l => ({ name: l.product_name, category: l.category_name })));
    
    // Filter by category if not "All"
    if (categoryFilter !== 'All') {
      filteredListings = listingsArray.filter(l => {
        // Case-insensitive comparison
        const listingCategory = (l.category_name || '').toLowerCase();
        const filterCategory = categoryFilter.toLowerCase();
        return listingCategory === filterCategory;
      });
      console.log('Filtered results:', filteredListings.length, 'items');
    }
    
    const dynamicSections = [];
    
    // If no filtered results, set empty sections (don't fallback to static)
    if (filteredListings.length === 0) {
      setSections([]);
      return;
    }
    
    // Trending section - sorted by engagement
    const trendingListings = [...filteredListings]
      .sort((a, b) => (b.view_count + b.love_count) - (a.view_count + a.love_count))
      .slice(0, 6);
    
    if (trendingListings.length > 0) {
      dynamicSections.push({
        id: 'trending',
        title: categoryFilter === 'All' ? 'Trending Now' : `Trending in ${categoryFilter}`,
        items: trendingListings.map(transformListingToItem)
      });
    }
    
    // Products section
    const products = filteredListings.filter(l => l.type === 'PRODUCT').slice(0, 6);
    if (products.length > 0) {
      dynamicSections.push({
        id: 'products',
        title: categoryFilter === 'All' ? 'Products' : `${categoryFilter} Products`,
        items: products.map(transformListingToItem)
      });
    }
    
    // Services section
    const services = filteredListings.filter(l => l.type === 'SERVICE').slice(0, 6);
    if (services.length > 0) {
      dynamicSections.push({
        id: 'services',
        title: categoryFilter === 'All' ? 'Services' : `${categoryFilter} Services`,
        items: services.map(transformListingToItem)
      });
    }
    
    setSections(dynamicSections);
  };

  const loadDynamicData = async () => {
    try {
      setLoading(true);
      
      // Fetch categories, listings, and wishlist status
      const [categoriesData, listingsData, wishlistData] = await Promise.all([
        categoriesAPI.getAll(),
        listingAPI.getAll({ limit: 100 }),
        wishlistAPI.getStatus().catch(() => ({ wishlist_ids: [] }))
      ]);

      const categoriesList = categoriesData.categories || [];
      const listingsArray = listingsData.listings || [];
      const wishlistIdsList = wishlistData.wishlist_ids || [];
      
      setCategories(categoriesList);
      setAllListings(listingsArray);
      setWishlistIds(wishlistIdsList);
      
      // Organize sections with initial filter
      organizeSections(listingsArray, selectedCategory);
      
    } catch (error) {
      console.error('Error loading data:', error);
      setSections(STATIC_SECTIONS);
    } finally {
      setLoading(false);
    }
  };

  const handleToggleWishlist = async (listingId) => {
    const isCurrentlyWishlisted = wishlistIds.includes(listingId);
    
    try {
      if (isCurrentlyWishlisted) {
        await wishlistAPI.remove(listingId);
        setWishlistIds(wishlistIds.filter(id => id !== listingId));
        toast.success('Removed from wishlist');
      } else {
        await wishlistAPI.add(listingId);
        setWishlistIds([...wishlistIds, listingId]);
        toast.success('Added to wishlist');
      }
    } catch (error) {
      console.error('Error toggling wishlist:', error);
      toast.error('Failed to update wishlist');
    }
  };

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
  };

  const handleCardClick = async (reel) => {
    try {
      // Track click count
      if (reel.listing_id) {
        await watchAPI.click(reel.listing_id);
      }
      
      // Navigate to Watch section with the specific video
      navigate(`/watch?video=${reel.listing_id}`);
    } catch (error) {
      console.error('Error tracking click:', error);
      // Still navigate even if tracking fails
      navigate(`/watch?video=${reel.listing_id}`);
    }
  };

  const handleShowMore = (reel) => {
    const influencer = reel.influencerName;
    const products = INFLUENCER_PRODUCTS[influencer] || INFLUENCER_PRODUCTS['default'];
    setSelectedInfluencer(influencer);
    setSelectedProducts(products);
    setSheetOpen(true);
  };

  const handleCloseSheet = () => {
    setSheetOpen(false);
    setTimeout(() => {
      setSelectedInfluencer(null);
      setSelectedProducts([]);
    }, 300);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center star-particles">
        <Loader className="w-8 h-8 animate-spin text-purple-500" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      <CategoryPills 
        categories={categories}
        selected={selectedCategory}
        onSelect={handleCategorySelect}
      />
      <QuickBubbles 
        categories={categories}
        onSelect={handleCategorySelect}
      />
      
      <div className="space-y-2">
        {sections.length > 0 ? (
          sections.map((section) => (
            <SectionCarousel 
              key={section.id} 
              section={section} 
              onShowMore={handleShowMore}
              onCardClick={handleCardClick}
              wishlistIds={wishlistIds}
              onToggleWishlist={handleToggleWishlist}
            />
          ))
        ) : (
          <div className="text-center py-20 px-4 relative z-10">
            <div className="max-w-md mx-auto">
              <div className="w-20 h-20 bg-purple-600 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-10 h-10 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-white mb-2 font-inter">
                No listings in {selectedCategory}
              </h3>
              <p className="text-gray-400 font-inter text-sm mb-6">
                {selectedCategory === 'All' 
                  ? 'No listings available yet. Be the first to create one!' 
                  : `There are no listings in the ${selectedCategory} category yet. Try another category or be the first to list here!`}
              </p>
              <button
                onClick={() => setSelectedCategory('All')}
                className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-2 rounded-full font-inter font-medium transition-colors"
              >
                View All Categories
              </button>
            </div>
          </div>
        )}
      </div>
      
      <ProductSheet
        isOpen={sheetOpen}
        onClose={handleCloseSheet}
        influencer={selectedInfluencer}
        products={selectedProducts}
      />
    </div>
  );
};

export default Home;

