import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import { AuthProvider } from './context/AuthContext';
import ProtectedRoute from './components/ProtectedRoute';
import AdminLayout from './components/AdminLayout';
import BottomNav from './components/BottomNav';
import Login from './pages/Login';
import Signup from './pages/Signup';
import Home from './pages/Home';
import Watch from './pages/Watch';
import Listing from './pages/Listing';
import UploadListing from './pages/UploadListing';
import MyListings from './pages/MyListings';
import ProviderRegistration from './pages/ProviderRegistration';
import Wishlist from './pages/Wishlist';
import Profile from './pages/Profile';
import AdminDashboard from './pages/AdminDashboard';
import AdminReports from './pages/AdminReports';
import AdminSettings from './pages/AdminSettings';
import UserManagement from './pages/UserManagement';
import InfluencerManagement from './pages/InfluencerManagement';
import BrandManagement from './pages/BrandManagement';
import VideoManagement from './pages/VideoManagement';
import SubscriptionManagement from './pages/SubscriptionManagement';
import PromotionManagement from './pages/PromotionManagement';
import PaymentManagement from './pages/PaymentManagement';
import NotificationManagement from './pages/NotificationManagement';
import CMSManagement from './pages/CMSManagement';
import InfluencerVideoRequest from './pages/InfluencerVideoRequest';
import BrandVideoRequest from './pages/BrandVideoRequest';
import PromotionSubmission from './pages/PromotionSubmission';

function App() {
  return (
    <Router>
      <AuthProvider>
        <div className="flex flex-col min-h-screen">
          <Toaster
            position="top-center"
            toastOptions={{
              duration: 3000,
              style: {
                background: '#1a1a1a',
                color: '#fff',
                borderRadius: '12px',
                padding: '16px',
                border: '1px solid #4a4a4a',
              },
              success: {
                iconTheme: {
                  primary: '#7c3aed',
                  secondary: '#fff',
                },
              },
              error: {
                iconTheme: {
                  primary: '#ef4444',
                  secondary: '#fff',
                },
              },
            }}
          />
          <Routes>
            {/* Admin Routes - Protected by existing auth system */}
            <Route
              path="/admin/*"
              element={
                <ProtectedRoute adminOnly={true}>
                  <AdminLayout>
                    <Routes>
                      <Route path="/dashboard" element={<AdminDashboard />} />
                      <Route path="/reports" element={<AdminReports />} />
                      <Route path="/settings" element={<AdminSettings />} />
                      <Route path="/users" element={<UserManagement />} />
                      <Route path="/influencers" element={<InfluencerManagement />} />
                      <Route path="/brands" element={<BrandManagement />} />
                      <Route path="/videos" element={<VideoManagement />} />
                      <Route path="/subscriptions" element={<SubscriptionManagement />} />
                      <Route path="/promotions" element={<PromotionManagement />} />
                      <Route path="/payments" element={<PaymentManagement />} />
                      <Route path="/notifications" element={<NotificationManagement />} />
            <Route path="/cms" element={<CMSManagement />} />
                      <Route path="/" element={<Navigate to="/admin/dashboard" replace />} />
                      <Route path="*" element={<Navigate to="/admin/dashboard" replace />} />
                    </Routes>
                  </AdminLayout>
                </ProtectedRoute>
              }
            />

            {/* Regular App Routes */}
            <Route path="/login" element={<Login />} />
            <Route path="/signup" element={<Signup />} />
            <Route
              path="/*"
              element={
                <div className="flex flex-col min-h-screen">
                  <div className="flex-1 pb-24">
                    <Routes>
                      <Route
                        path="/"
                        element={
                          <ProtectedRoute>
                            <Home />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/watch"
                        element={
                          <ProtectedRoute>
                            <Watch />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/provider/register"
                        element={
                          <ProtectedRoute>
                            <ProviderRegistration />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/listing"
                        element={
                          <ProtectedRoute>
                            <Listing />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/listing/upload"
                        element={
                          <ProtectedRoute>
                            <UploadListing />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/listing/my"
                        element={
                          <ProtectedRoute>
                            <MyListings />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/wishlist"
                        element={
                          <ProtectedRoute>
                            <Wishlist />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/profile"
                        element={
                          <ProtectedRoute>
                            <Profile />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/influencer/video-request"
                        element={
                          <ProtectedRoute>
                            <InfluencerVideoRequest />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/brand/video-request"
                        element={
                          <ProtectedRoute>
                            <BrandVideoRequest />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/promotion-submission"
                        element={
                          <ProtectedRoute>
                            <PromotionSubmission />
                          </ProtectedRoute>
                        }
                      />
                      <Route path="*" element={<Navigate to="/" replace />} />
                    </Routes>
                  </div>
                  <BottomNav />
                </div>
              }
            />
          </Routes>
        </div>
      </AuthProvider>
    </Router>
  );
}

export default App;


